import {React, useEffect, useState} from "react";
import {redirect, Route, Routes, useNavigate} from 'react-router-dom';

import ProtectedRouteElement from "./components/ProtectedRoute/ProtectedRoute";

import './App.css';

import Lending from "./pages/Lending/Lending";
import PrivacyPolicy from "./pages/PrivacyPolicy/PrivacyPolicy";
import LicenseAgreement from "./pages/LicenseAgreement/LicenseAgreement";
import Auth from './pages/Auth/Auth';
import Main from './pages/Main/Main'
import Statistics from './pages/Statistics/Statistics';
import Authorization from './components/Authorization/Authorization';
import ResetPass from './components/ResetPass/ResetPass';
import ConfirmEmail from './components/СonfirmEmail/ConfirmEmail';
import ChangePassword from './components/СhangePassword/СhangePassword';
import DeleteAsset from "./components/DeleteConfirmation/DeleteAsset";
import Setting from "./pages/Settings/Setting";
import RealtyEdit from "./components/Popup/Realty/RealtyEdit/RealtyEdit";
import TransportEdit from "./components/Popup/TransportForm/TransportEdit/TransportEdit";
import DepositEdit from "./components/Popup/DepositForm/DepositEdit/DepositEdit";
import DigitalRubleEdit from "./components/Popup/DigitalRuble/DigitalRubleEdit/DigitalRubleEdit";

import StatisticPage from "./pages/Statistic/StatisticPage";
import CryptoCurrencyEdit from "./components/Popup/СryptoСurrency/CryptoCurrencyEdit/CryptoCurrencyEdit";

import * as api from './api/api';

import { useDispatch, useSelector } from "react-redux";

import {fetchData, rocketSlice} from "./store/reducers/RocetSlice";

import { log, loginOut, patchInfo } from "./store/reducers/user";
import { signUp } from "./store/authActions/register";
import { signIn } from "./store/authActions/login";
import { changeLoginSwitch } from "./store/reducers/loginSwitch";
import NotFound from "./components/NotFound/NotFound";

function App() {

const darkTheme = useSelector((state) => state.themeSlice.theme)
const user = useSelector((state) => state.userSlice.user)

const isLogin = useSelector((state) => state.loginSwitchSlice.loginSwitch)

  const rocketData = useSelector((state) => state.rocketSlice.data);

  const dispatch = useDispatch()
  const navigate = useNavigate()

  useEffect(() => {
      api.getUser()
      .then((res) => {
        dispatch(log(res.data))
        dispatch(fetchData())
      })
      .catch((error) => {
        navigate('/')
        console.log(error)
      })
  }, [])

  useEffect(() => {

  }, [])

  const [newActivePopupOpened, setNewActivePopupOpened] = useState(false);
  const [changePasswordPopupOpened, setChangePasswordPopupOpened] = useState(true);
  const [openModal, setOpenModal] = useState(false);
  const [openChange, setOpenChange] = useState(false)
  const [openNotification, setOpenNotification] = useState(false)

  function hendleActiveModal () {
    setOpenModal(true)
  }
  function closeModal () {
    setOpenModal(false);
  }

  function activeOpenChange () {
    setOpenChange(true)
  }

  function disabledOpenChange () {
    setOpenChange(false)
  }

  function handleNewActivePopup() {
    setNewActivePopupOpened(true)
  }
  function handleChangePasswordPopup() {
    setChangePasswordPopupOpened(true)
  }
  function handleOpenNotification() {
    setOpenNotification(true)
  }

  function closePopups() {
    setNewActivePopupOpened(false)
    setChangePasswordPopupOpened(false)
    setOpenNotification(false)
  }
///////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
// РЕГИСТРАЦИЯ
function register(email, password) {
  dispatch(signUp({email, password}))
  .then((res) => {
    if (res.error === undefined) {
       login(email, password)
    }
  })
}

// ЛОГИН
function login(email, password) {
  dispatch(signIn({email, password}))
    .then((res) => {
        if (res.error === undefined){
           dispatch(fetchData());
        }
    })
}

// СМЕНА ПАРОЛЯ
function changePassword(password, newPassword) {
  api.changePassword(password, newPassword)
  .then(() => {
    setResult(true)
  })
  .catch((error) => {
    setResult(false)
    console.log(error)
  })
}

function logOut() {
  sessionStorage.removeItem('token')
  dispatch(loginOut())
dispatch(changeLoginSwitch(true))

  window.location.replace('/');
}

function patchUser(form) {
  api.patchUser(form)
  .then((res) => {
    console.log(res.data)
    dispatch(patchInfo(res.data))
  })
  .catch((error) => {
    console.log(error)
  })
}

function patchUserAgree(agree) {
  api.patchUserAgree(agree)
  .then((res) => {
    dispatch(patchInfo(res.data))
  })
  .catch((error) => {
    console.log(error)
  })
}

function deleteUser() {
  api.deleteUser()
  .then(() => {
    sessionStorage.removeItem('token')
    closeModal()
    dispatch(loginOut())
    navigate('/auth')

  })
  .catch((error) => {
    console.log(error)
  })
}

///////////////////////////////////////////////////////////////////////////////////
// статус ответа. ок или ошибка? нужен для попапов подтверждения
const [result, setResult] = useState(true)


  return ( 
    <>
      <div className={!darkTheme ? 'page page_theme_ligth' : 'page page_theme_dark'}>
        <div className="page__content">
          <Routes>
        <Route path='/' element={ <Lending  /> } />
        <Route path='/privacy-policy' element={ <PrivacyPolicy /> }/>
        <Route path='/license-agreement' element={ <LicenseAgreement /> }/>

        <Route path='/auth' element={ <Auth element={ <Authorization  handleLogin={login} isLogin={isLogin} handleRegister={register}/> }/> } />
        <Route path='/reset' element ={ <Auth element={ <ResetPass /> } /> } />
        <Route path='/confirmPass' element={ <Auth element={ <ConfirmEmail /> } /> } />



        <Route path='/main' element={ <ProtectedRouteElement loggedIn={isLogin}  element={
          <Main
          // allActives ={allActives}
          // allDeposits={allDeposits}
          // allTransport={allTransport}
          // digitalRubles={digitalRubles}

            rocketData={rocketData}
            logOut ={logOut}
            isOk ={result}
            setIsOk={setResult}

            handleOpenNotification={handleOpenNotification}
            openNotification={openNotification}
            // switchTheme={switchTheme}
            handlerOpenPopup={handleNewActivePopup}
            newActivePopupOpened={newActivePopupOpened}
            closePopups={closePopups}
          /> 
      } />
        } />
        <Route path='/setting' element={ <ProtectedRouteElement loggedIn={isLogin} element={
          <Setting
          deleteUser={deleteUser}
          patchUserAgree={patchUserAgree}
          patchUser = {patchUser}
          logOut ={logOut}
          isOk ={result}
          changePassword={changePassword}

          closePopups={closePopups}
          handleOpenNotification={handleOpenNotification}
          openNotification={openNotification}
          // switchTheme={switchTheme}

          hendlerOpenModal = {hendleActiveModal}
          openModal={openModal}
          closeModal={closeModal}

          hendlerOpenChange={activeOpenChange}
          openChange={openChange}
          closeChange={disabledOpenChange}
          />
        }/>
      } />
        <Route path='/statistics' element={ <ProtectedRouteElement loggedIn={isLogin} element={
           <Statistics 
           logOut ={logOut}
           handleOpenNotification={handleOpenNotification}
           openNotification={openNotification}
           closePopups={closePopups}
            // switchTheme={switchTheme}
          /> 
        } />
         } />
         
        <Route path='/stat' element ={ <StatisticPage />} />
        <Route path='/realty/edit' element={ <ProtectedRouteElement loggedIn={isLogin} element={
        <RealtyEdit isOpened={true} realtyCategory={"Жилое"}/>
        }/>
      }/>
        <Route path='/transport/edit' element={ <ProtectedRouteElement loggedIn={isLogin} element={
        <TransportEdit isOpened={true} transportCategory={"Автомобиль"}
        />}/>
      }/>
        <Route path='/deposit/edit' element={ <ProtectedRouteElement loggedIn={isLogin} element={
        <DepositEdit isOpened={true} depositCategory="metal"/>
        }/>
      }/>
        <Route path='/digital/edit' element={ <ProtectedRouteElement loggedIn={isLogin} element={
        <DigitalRubleEdit isOpened={true}/>
        }/>
      }/>
        <Route path='/crypto/edit' element={ <ProtectedRouteElement loggedIn={isLogin} element={
        <CryptoCurrencyEdit isOpened={true}/>
        }/>
      }/>
        <Route path='/delete/asset' element={ <ProtectedRouteElement loggedIn={isLogin}  element={
        <DeleteAsset isOpened={true} page={1}/>
        }/>
      }/>

      <Route path="*" element={ <NotFound />} />
      </Routes>
      </div>
    </div>
   </>
  );
}

export default App;
