import React from "react";
import { useNavigate } from "react-router-dom";

import './NotFound.css'

function NotFound() {

  const navigate = useNavigate();

  function navigateToLending() {
    navigate('/')
  }

  return (
    <div className="not-found">
      <h2 className="title not-found_title">Страница не найдена!</h2>
      <p className="paragraph paragraph_l">К сожалению, запрашиваемая страница не найдена.</p>
      <button type="button" className="orange-button button button-s not-found_button" onClick={navigateToLending}>Перейти на главную</button>
    </div>
  )
}

export default NotFound;