import {React, useState, useEffect, useContext} from "react";

import './Popup.css';

import TypeCategory from "./TypeCategory/TypeCategory";


function Popup({
   isOpened,
   isClose,
   isOk,
   setIsOk,
   getBrands,
   brands,
   getCities,
   cities,
   getModels,
   models,}) {


  const [activeCategory, setActiveCategory] = useState('');
  const [realtyCategory, setRealtyCategory] = useState('');
  const [transportCategory, setTransportCategory] = useState('');
  const [depositCategory, setDepositCategory] = useState('');
  const [cryptoCurrency, setCryptoCurrency] = useState('');
  const [cryptoCurrencyMarket, setCryptoCurrencyMarket] = useState('')
  const [stocksCategory, setStocksCategory] = useState('')

  function handleClose() {
    setActiveCategory('')
    setRealtyCategory('')
    isClose()
  }


  return (
    <div className={isOpened ? "popup popup_opened" : "popup" }>
      <div className="popup__container">
         <TypeCategory

         isOk={isOk}
         setIsOk={setIsOk}

            handleClose={handleClose}
            
            activeCategory={activeCategory}
            setActiveCategory={setActiveCategory}

            realtyCategory={realtyCategory}
            setRealtyCategory={setRealtyCategory}

            transportCategory={transportCategory}
            setTransportCategory={setTransportCategory}

            depositCategory={depositCategory}
            setDepositCategory={setDepositCategory}

            cryptoCurrency={cryptoCurrency}
            setCryptoCurrency={setCryptoCurrency}
            cryptoCurrencyMarket={cryptoCurrencyMarket}
            setCryptoCurrencyMarket={setCryptoCurrencyMarket}

            brands = {brands}
            cities = {cities}
            models = {models}
            stocksCategory={stocksCategory}
            setStocksCategory={setStocksCategory}/>
      </div>
      
    </div>
  )
}

export default Popup;