import {createSlice} from "@reduxjs/toolkit";

import {
    addDepositMetalsReplenishment,
    addDepositMetalsWithdrawal,
    addDepositReplenishment,
    addDepositWithdrawal, getCar, getCommercials, getDeposits, getFlats, getMetals, getMotorcycles, getPlots,
    getRocket, getVillas,
    postBike, postBinance,
    postCar, postCommercial,
    postDeposit,
    postDigitalRuble, postFinam, postFlats,
    postMetals, postPlots, postTinkoff, postVillas,
    putBike,
    putCar, putCommercial,
    putDeposit,
    putDepositMetals,
    putDigitalRuble, putFlats, putPlots, putVillas,
    removeDepositReplenishment,
    removeDepositWithdrawal,
    removeMetalReplenishment,
    removeMetalWithdrawal
} from "../../api/api";



export const rocketSlice = createSlice({
    name:'rocket',
    initialState: {
        data: {},
        loading: false,
        error: null,

        allActives: {
          objects: {},
          total_quantity: 0,
          total_current_price: 0,
          total_profit_amount: 0,
          total_profit_percentage: 0,
        },
        realty:{
            objects: {},
            quantity: 0,
            current_price: 0
        },
        transport: {
          objects: {},
          quantity: 0,
          current_price: 0
        },
        deposits: {
          objects: {},
          quantity: 0,
          current_price: 0
        },
        markets: {
            objects: {},
            quantity: 0,
            current_price: 0
        },
        flats:{},
        commercials:{},
        villas: {},
        plots:{},
        digital_rubles: {},
        cars: {},
        motorcycles: {},
        bank_deposits: {},
        metal_accounts: {},
        activeId:{}
    },

    reducers:{
        setData: (state, action) => {
          state.data = action.payload;
          state.loading = false;
          state.error = null;
        },
        setLoading: (state) => {
          state.loading = true;
          state.error = null;
        },
        setError: (state, action) => {
          state.loading = false;
          state.error = action.payload;
        },
        setActiveId: (state,action) => {
            state.activeId = action.payload
        },
        allActives: (state, action) => {
          const addTypeCar = action.payload.cars.objects.map((item) => ({...item, "type":"car"}))
          const addTypeBike = action.payload.motorcycles.objects.map((item) => ({...item, "type":"bike"}))
          const addTypeDeposit = action.payload.bank_deposits.objects.map((item) => ({...item, "type":"money"}))
          const addTypeMetal = action.payload.metal_accounts.objects.map((item) => ({...item, "type":"metals"}))
          const addTypeDigital = action.payload.digital_rubles.objects.map((item) => ({...item, "type":"digital-ruble"}))
          const addTypeHouse = action.payload.villas.objects.map((item) => ({...item, "type":"house"}))
          const addTypeRealty = action.payload.flats.objects.map((item) => ({...item, "type":"realty"}))
          const addTypeCommercial = action.payload.commercials.objects.map((item) => ({...item, "type":"commercial"}))
          const addTypeLandPlot = action.payload.plots.objects.map((item) => ({...item, "type":"landPlot"}))


          state.allActives.objects = [...addTypeCar, ...addTypeBike, ...addTypeDeposit, ...addTypeMetal, ...addTypeDigital, ...addTypeCommercial, ...addTypeHouse, ...addTypeLandPlot, ...addTypeRealty]

          state.allActives.total_quantity = action.payload.motorcycles.quantity + action.payload.cars.quantity + action.payload.bank_deposits.quantity + action.payload.metal_accounts.quantity + action.payload.digital_rubles.quantity + action.payload.villas.quantity + action.payload.flats.quantity + action.payload.commercials.quantity +  action.payload.plots.quantity

          state.allActives.total_current_price = action.payload.motorcycles.current_price + action.payload.cars.current_price + action.payload.bank_deposits.current_price + action.payload.metal_accounts.current_price + action.payload.digital_rubles.current_price

          state.allActives.total_profit_amount = action.payload.motorcycles.profit_amount + action.payload.cars.profit_amount + action.payload.bank_deposits.profit_amount + action.payload.metal_accounts.profit_amount + action.payload.digital_rubles.profit_amount + action.payload.villas.profit_amount + action.payload.flats.profit_amount + action.payload.commercials.profit_amount +  action.payload.plots.profit_amount

          state.allActives.total_profit_percentage = action.payload.motorcycles.profit_percentage + action.payload.cars.profit_percentage + action.payload.bank_deposits.profit_percentage + action.payload.metal_accounts.profit_percentage + action.payload.digital_rubles.profit_percentage + action.payload.villas.profit_percentage + action.payload.flats.profit_percentage + action.payload.commercials.profit_percentage +  action.payload.plots.profit_percentage
        },
        realty: (state, action) => {
            const addTypeApartment = action.payload.flats.objects.map((item) => ({...item, "type":"realty"}))
            const addTypeCommercial = action.payload.commercials.objects.map((item) => ({...item, "type":"commercial"}))
            const addTypeVilla = action.payload.villas.objects.map((item) => ({...item, "type":"house"}))
            const addTypePlot = action.payload.plots.objects.map((item) => ({...item, "type":"landPlot"}))

            state.realty.objects = [...addTypeApartment, ...addTypeCommercial, ...addTypeVilla, ...addTypePlot]
            state.realty.quantity = action.payload.flats.quantity + action.payload.commercials.quantity + action.payload.villas.quantity + action.payload.plots.quantity
            state.realty.current_price = action.payload.flats.current_price + action.payload.commercials.current_price + action.payload.villas.current_price + action.payload.plots.current_price
        },
        transport: (state, action) =>{
          const addTypeCar = action.payload.cars.objects.map((item) => ({...item, "type":"car"}))
          const addTypeBike = action.payload.motorcycles.objects.map((item) => ({...item, "type":"bike"}))

          state.transport.objects = [...addTypeCar, ...addTypeBike]
          state.transport.quantity = action.payload.motorcycles.quantity + action.payload.cars.quantity
          
          if (action.payload.cars.current_price === undefined && action.payload.motorcycles.current_price === undefined) {
            state.transport.current_price = 0
          } else if (action.payload.cars.current_price === undefined){
            state.transport.current_price = action.payload.motorcycles.current_price
          } else if (action.payload.motorcycles.current_price === undefined){
            state.transport.current_price = action.payload.cars.current_price
          } else {
            state.transport.current_price = action.payload.cars.current_price + action.payload.motorcycles.current_price
          }
        },

        deposits: (state,action) => {
          const addTypeDeposit = action.payload.bank_deposits.objects.map((item) => ({...item, "type":"money"}))
          const addTypeMetal = action.payload.metal_accounts.objects.map((item) => ({...item, "type":"metals"}))
          state.deposits.objects = [...addTypeDeposit, ...addTypeMetal]
          state.deposits.quantity = action.payload.bank_deposits.quantity + action.payload.metal_accounts.quantity
          console.log(action.payload.metal_accounts.current_price)
          if (action.payload.bank_deposits.current_price === undefined && action.payload.metal_accounts.current_price === undefined) {
            state.deposits.current_price = 0
          } else if (action.payload.bank_deposits.current_price === undefined){
            state.deposits.current_price = action.payload.metal_accounts.current_price
          } else if (action.payload.metal_accounts.current_price === undefined){
            state.deposits.current_price = action.payload.bank_deposits.current_price
          } else {
            state.deposits.current_price = action.payload.bank_deposits.current_price + action.payload.metal_accounts.current_price
          }


        },

        digital_rubles: (state, action) => {
          const digital = action.payload.objects.map((item) => ({...item, 'type': 'digital-ruble'}))
          state.digital_rubles = {...action.payload, 'objects': digital}
        },
        addDigital: (state,action) => {
          const digital = {...action.payload, 'type': 'digital-ruble'}
          state.digital_rubles.objects.push(digital)
          state.allActives.objects.push(digital)

          state.digital_rubles.quantity += 1
          if (state.digital_rubles.current_price === undefined) {
            state.digital_rubles.current_price = 0 + action.payload.current_amount
          } else {
          state.digital_rubles.current_price += action.payload.current_amount
          }
          state.allActives.total_quantity += 1
          state.allActives.total_current_price += action.payload.current_amount
        },
        editDigital: (state, action) => {

            const editedDigitalIndex = state.digital_rubles.objects.findIndex(digital => digital.id === action.payload.id);
            const editedAllActivesIndex = state.allActives.objects.findIndex(digital => digital.type === 'digital-ruble' && digital.id === action.payload.id )

            if (editedDigitalIndex !== -1) {
                state.digital_rubles.objects[editedDigitalIndex] = {...action.payload, 'type': 'digital-ruble', 'profit_amount':0};
                state.allActives.objects[editedAllActivesIndex] = {...action.payload, 'type': 'digital-ruble', 'profit_amount':0};
            }
        },

        flats: (state,action) => {
            state.flats = action.payload
        },
        addFlats: (state, action) => {
            const flats = {...action.payload, 'type': 'realty'}
            state.flats.objects.push(flats)
            state.allActives.objects.push(flats)
            state.realty.objects.push(flats)

            state.flats.quantity += 1
            state.flats.current_price += action.payload.price

            state.realty.quantity += 1
            state.realty.current_price += action.payload.price

            state.allActives.total_quantity += 1
            state.allActives.total_current_price += action.payload.price
        },
        editFlats: (state, action) => {
            const findIndexFlats = state.flats.objects.findIndex(flats => flats.id === action.payload.id)

            state.flats.objects[findIndexFlats] = action.payload
        },

        commercial: (state, action) => {
            state.commercials = action.payload
        },
        addCommercial: (state, action) => {
            const commercials = {...action.payload, 'type': 'commercials'}
            state.commercials.objects.push(commercials)
            state.allActives.objects.push(commercials)
            state.realty.objects.push(commercials)

            state.commercials.quantity += 1
            state.commercials.current_price += action.payload.price

            state.realty.quantity += 1
            state.realty.current_price += action.payload.price

            state.allActives.total_quantity += 1
            state.allActives.total_current_price += action.payload.price
        },
        editCommercial: (state, action) => {
            const findIndexCommercial = state.commercials.objects.findIndex(commercials => commercials.id === action.payload.id)

            state.commercials.objects[findIndexCommercial] = action.payload
        },

        villas: (state, action) => {
            state.villas = action.payload
        },
        addVillas: (state, action) => {
            const villas = {...action.payload, 'type': 'house'}
            state.villas.objects.push(villas)
            state.allActives.objects.push(villas)
            state.realty.objects.push(villas)

            state.villas.quantity += 1
            state.villas.current_price += action.payload.price

            state.realty.quantity += 1
            state.realty.current_price += action.payload.price

            state.allActives.total_quantity += 1
            state.allActives.total_current_price += action.payload.price
        },
        editVillas: (state, action) => {
            const findIndexVillas = state.villas.objects.findIndex(villas => villas.id === action.payload.id)

            state.villas.objects[findIndexVillas] = action.payload
        },

        plots: (state, action) => {
            state.plots = action.payload
        },
        addPlots: (state, action) => {
            const plots = {...action.payload, 'type': 'landPlot'}
            state.plots.objects.push(plots)
            state.allActives.objects.push(plots)
            state.realty.objects.push(plots)

            state.plots.quantity += 1
            state.plots.current_price += action.payload.price

            state.realty.quantity += 1
            state.realty.current_price += action.payload.price
        },
        editPlots: (state, action) => {
            const findIndexPlots = state.plots.objects.findIndex(plots => plots.id === action.payload.id)

            state.plots.objects[findIndexPlots] = action.payload
        },

        cars: (state, action) => {
          state.cars = action.payload
        },
        addCars: (state, action) => {
          const car = {...action.payload, 'type': 'car'}
            state.cars.objects.push(car)
            state.allActives.objects.push(car)
            state.transport.objects.push(car)

            state.cars.quantity += 1
            state.cars.current_price += action.payload.price

            state.transport.quantity += 1
            state.transport.current_price += action.payload.price

            state.allActives.total_quantity += 1
            state.allActives.total_current_price += action.payload.price
        },
        editCars: (state, action) => {
            const findIndexCars = state.cars.objects.findIndex(cars => cars.id === action.payload.id)

            state.cars.objects[findIndexCars] = action.payload
        },

        motorcycles: (state, action) => {
            state.motorcycles = action.payload
        },
        addBike: (state, action) => {
          const bike = {...action.payload, 'type': 'bike'}
            state.motorcycles.objects.push(bike)
            state.allActives.objects.push(bike)
            state.transport.objects.push(bike)

            state.motorcycles.quantity += 1
            state.motorcycles.current_price += action.payload.price

            state.transport.quantity += 1
            state.transport.current_price += action.payload.price

            state.allActives.total_quantity += 1
            state.allActives.total_current_price += action.payload.price
        },
        editBike: (state, action) => {
            const findIndexBike = state.motorcycles.objects.findIndex(motorcycles => motorcycles.id === action.payload.id)

            state.motorcycles.objects[findIndexBike] = action.payload
        },

        bank_deposits: (state, action) => {
            state.bank_deposits = action.payload
        },
        addDeposits: (state, action) => {
          const deposit = {...action.payload, 'type': 'money'}
            state.bank_deposits.objects.push(deposit)
            state.allActives.objects.push(deposit)
            state.deposits.objects.push(deposit)

            state.bank_deposits.quantity += 1
            state.bank_deposits.current_price += action.payload.statistic[0].current_price

            state.deposits.quantity += 1
            state.deposits.current_price += action.payload.statistic[0].current_price

            state.allActives.total_quantity += 1
            state.allActives.total_current_price += action.payload.statistic[0].current_price
        },
        editDeposit: (state, action) => {
            const findIndexDeposit = state.bank_deposits.objects.findIndex(deposits => deposits.id === action.payload.id)

            state.bank_deposits.objects[findIndexDeposit] = action.payload
        },
        addDepositReplenishments: (state, action) => {
            const depositReplenishment = {...action.payload}
            const editedDepositIndex = state.bank_deposits.objects.findIndex(deposits => deposits.id === state.activeId[0].id);


            state.bank_deposits.objects[editedDepositIndex].refills.push(depositReplenishment)
            state.bank_deposits.objects[editedDepositIndex].statistic[0].current_price += action.payload.refill_amount

            state.activeId[0].statistic[0].current_price += action.payload.refill_amount
            state.activeId[0].refills.unshift(depositReplenishment)
        },
        deleteDepositReplenishments:(state, action) => {
            const depositReplenishmentId = action.payload.id;
            const editedDepositIndex = state.bank_deposits.objects.findIndex(metals => metals.id === state.activeId[0].id);

            const replenishmentIndex = state.bank_deposits.objects[editedDepositIndex].refills.findIndex(replenishment => replenishment.id === depositReplenishmentId);

            if (replenishmentIndex !== -1) {
                state.bank_deposits.objects[editedDepositIndex].refills.splice(replenishmentIndex, 1);
                state.bank_deposits.objects[editedDepositIndex].statistic[0].current_price -= action.payload.refill_amount

                const removedReplenishment = state.activeId[0].refills.find(replenishment => replenishment.id === depositReplenishmentId);
                if (removedReplenishment) {
                    state.activeId[0].statistic[0].current_price -= removedReplenishment.refill_amount;
                    state.activeId[0].refills = state.activeId[0].refills.filter(replenishment => replenishment.id !== depositReplenishmentId);
                }
            }

        },
        addDepositWithdrawals: (state, action) => {
            const depositWithdrawal = {...action.payload}
            const editedDepositIndex = state.bank_deposits.objects.findIndex(deposits => deposits.id === state.activeId[0].id);

            state.bank_deposits.objects[editedDepositIndex].withdrawals.push(depositWithdrawal)
            state.bank_deposits.objects[editedDepositIndex].statistic[0].current_price -= action.payload.withdrawal_amount

            state.activeId[0].statistic[0].current_price -= action.payload.withdrawal_amount
            state.activeId[0].withdrawals.unshift(depositWithdrawal)
        },
        deleteDepositWithdrawals: (state, action) => {
            const depositWithdrawalsId = action.payload.id;
            const editedDepositIndex = state.bank_deposits.objects.findIndex(metals => metals.id === state.activeId[0].id);

            const replenishmentIndex = state.bank_deposits.objects[editedDepositIndex].withdrawals.findIndex(replenishment => replenishment.id === depositWithdrawalsId);

            if (replenishmentIndex !== -1) {
                state.bank_deposits.objects[editedDepositIndex].withdrawals.splice(replenishmentIndex, 1);
                state.bank_deposits.objects[editedDepositIndex].statistic[0].current_price += action.payload.withdrawal_amount

                const removedWithdrawals = state.activeId[0].withdrawals.find(withdrawals => withdrawals.id === depositWithdrawalsId);
                if (removedWithdrawals) {
                    state.activeId[0].statistic[0].current_price += removedWithdrawals.withdrawal_amount;
                    state.activeId[0].withdrawals = state.activeId[0].withdrawals.filter(withdrawals => withdrawals.id !== depositWithdrawalsId);
                }
            }
        },


        metal_accounts: (state, action) => {
            state.metal_accounts = action.payload
        },
        addMetals: (state, action) => {
          const metal = {...action.payload, 'type': 'metals'}
            state.metal_accounts.objects.push(metal)
            state.allActives.objects.push(metal)
            state.deposits.objects.push(metal)

            state.metal_accounts.quantity += 1
            state.metal_accounts.current_price += action.payload.statistic[0].current_price

            state.deposits.quantity += 1
            state.deposits.current_price += action.payload.statistic[0].current_price

            state.allActives.total_quantity += 1
            state.allActives.total_current_price += action.payload.statistic[0].current_price
        },
        editMetal: (state,action) => {
            const findIndexMetal = state.metal_accounts.objects.findIndex(deposits => deposits.id === action.payload.id)

            state.metal_accounts.objects[findIndexMetal] = action.payload
        },
        addMetalReplenishments: (state, action) => {
            const metalReplenishment = {...action.payload}
            const editedMetalIndex = state.metal_accounts.objects.findIndex(metals => metals.id === state.activeId[0].id);

            console.log(editedMetalIndex)
            state.metal_accounts.objects[editedMetalIndex].refills.push(metalReplenishment)
            state.metal_accounts.objects[editedMetalIndex].statistic[0].current_price += action.payload.refill_amount

            state.activeId[0].statistic[0].current_price += action.payload.refill_amount
            state.activeId[0].current_weight += action.payload.refill_weight
            state.activeId[0].refills.unshift(metalReplenishment)
        },
        deleteMetalReplenishments:(state, action) => {
            // const metalReplenishmentId = action.payload.id;
            // const editedMetalIndex = state.metal_accounts.objects.findIndex(metals => metals.id === state.activeId[0].id);
            //
            // const replenishmentIndex = state.metal_accounts.objects[editedMetalIndex].refills.findIndex(replenishment => replenishment.id === metalReplenishmentId);
            //
            // if (replenishmentIndex !== -1) {
            //     state.metal_accounts.objects[editedMetalIndex].refills.splice(replenishmentIndex, 1);
            //     state.metal_accounts.objects[editedMetalIndex].statistic[0].current_price -= action.payload.refill_weight
            //
            //     const removedReplenishment = state.activeId[0].refills.find(replenishment => replenishment.id === metalReplenishmentId);
            //     if (removedReplenishment) {
            //         state.activeId[0].statistic[0].current_price -= removedReplenishment.refill_amount;
            //         state.activeId[0].current_weight -= removedReplenishment.refill_weight;
            //         state.activeId[0].refills = state.activeId[0].refills.filter(replenishment => replenishment.id !== metalReplenishmentId);
            //     }
            // }
        },
        addMetalWithdrawals: (state, action) => {
            const metalWithdrawal = {...action.payload}
            const editedMetalIndex = state.metal_accounts.objects.findIndex(deposits => deposits.id === state.activeId[0].id);

            state.metal_accounts.objects[editedMetalIndex].withdrawals.push(metalWithdrawal)
            state.activeId[0].current_price -= action.payload.withdrawal_amount
            state.activeId[0].current_weight -= action.payload.withdrawal_weight
            state.activeId[0].withdrawals.unshift(metalWithdrawal)
        },
        deleteMetalWithdrawals: (state, action) => {
            const metalWithdrawalsId = action.payload.id;
            const editedMetalIndex = state.metal_accounts.objects.findIndex(metals => metals.id === state.activeId[0].id);

            const replenishmentIndex = state.metal_accounts.objects[editedMetalIndex].withdrawals.findIndex(replenishment => replenishment.id === metalWithdrawalsId);

            if (replenishmentIndex !== -1) {
                state.metal_accounts.objects[editedMetalIndex].withdrawals.splice(replenishmentIndex, 1);

                const removedWithdrawals = state.activeId[0].withdrawals.find(withdrawals => withdrawals.id === metalWithdrawalsId);
                if (removedWithdrawals) {
                    state.activeId[0].current_price += removedWithdrawals.withdrawal_amount;
                    state.activeId[0].current_weight += removedWithdrawals.withdrawal_weight;
                    state.activeId[0].withdrawals = state.activeId[0].withdrawals.filter(withdrawals => withdrawals.id !== metalWithdrawalsId);
                }
            }
        },

        markets: (state,action) => {
            state.markets = action.payload
        },
        addTinkoff:(state,action) => {
            const tinkoff = {...action.payload, 'type': 'tinkoff'}
            state.allActives.objects.push(tinkoff)
            state.markets.objects.push(tinkoff)

            state.markets.quantity += 1
            state.markets.current_price += action.payload.statistic[0].current_price

            state.allActives.total_quantity += 1
            state.allActives.total_current_price += action.payload.statistic[0].current_price
        },
        editTinkoff:(state,action) => {
            const findIndexTinkoff = state.markets.objects.findIndex(tinkoff => tinkoff.id === action.payload.id)

            state.markets.objects[findIndexTinkoff] = action.payload
        },
        addFinam:(state,action) => {
            const finam = {...action.payload, 'type': 'finam'}
            state.allActives.objects.push(finam)
            state.markets.objects.push(finam)

            state.markets.quantity += 1
            state.markets.current_price += action.payload.statistic[0].current_price

            state.allActives.total_quantity += 1
            state.allActives.total_current_price += action.payload.statistic[0].current_price
        },
        editFinam: (state, action) =>{
            const findIndexFinam = state.markets.objects.findIndex(finam => finam.id === action.payload.id)

            state.markets.objects[findIndexFinam] = action.payload
        },
        addBinance: (state, action) =>{
            const binance = {...action.payload, 'type': 'binance'}
            state.allActives.objects.push(binance)
            state.markets.objects.push(binance)

            state.markets.quantity += 1
            state.markets.current_price += action.payload.statistic[0].current_price

            state.allActives.total_quantity += 1
            state.allActives.total_current_price += action.payload.statistic[0].current_price
        },
        editBinance: (state,action) => {
            const findIndexBinance = state.markets.objects.findIndex(binance => binance.id === action.payload.id)

            state.markets.objects[findIndexBinance] = action.payload
        },
    },
});

export const { setData, setLoading, setError, allActives, transport, deposits, digital_rubles, bank_deposits, motorcycles, cars, metal_accounts, addCars, addBike, addDigital, addDeposits, addMetals, editCars, editBike, editDigital, editDeposit, editMetal, addDepositReplenishments, addDepositWithdrawals, setActiveId, addMetalReplenishments, addMetalWithdrawals, deleteMetalReplenishments, deleteMetalWithdrawals, deleteDepositReplenishments, deleteDepositWithdrawals, realty, flats, addFlats, addCommercial, commercial, addVillas, villas, addPlots, plots, editPlots, editCommercial, editFlats, editVillas, editFinam, addBinance, addFinam, addTinkoff, editBinance, editTinkoff, markets } = rocketSlice.actions;

export const fetchData = () => async (dispatch) => {
    try {
        const response = await getRocket();
        dispatch(setData(response.data));
        dispatch(allActives(response.data.assets))
        dispatch(transport(response.data.assets))
        dispatch(realty(response.data.assets))
        dispatch(deposits(response.data.assets))
        dispatch(digital_rubles(response.data.assets.digital_rubles))
        dispatch(flats(response.data.assets.flats))
        dispatch(villas(response.data.assets.villas))
        dispatch(plots(response.data.assets.plots))
        dispatch(commercial(response.data.assets.commercials))
        dispatch(motorcycles(response.data.assets.motorcycles))
        dispatch(cars(response.data.assets.cars))
        dispatch(markets(response.data.assets.markets))
        dispatch(bank_deposits(response.data.assets.bank_deposits))
        dispatch(metal_accounts(response.data.assets.metal_accounts))

        dispatch(setLoading(response.data));
    } catch (error) {
        dispatch(setError(error));
    }
};

