import {React, useState, useEffect} from "react";
import { Link } from "react-router-dom";

import './Header.css';
import avatar from "../../images/avatar.png";

import { changeTheme } from "../../store/reducers/theme";
import { useDispatch, useSelector } from "react-redux";
import { connect } from 'react-redux'

function Header({openNotification}) {

  const dispatch = useDispatch()
  const theme = useSelector((state) => state.themeSlice.theme)
  const user = useSelector((state) => state.userSlice.user)
  function switchTheme() { 
    dispatch(changeTheme())
  }

  const [bellActive, setBellActive] = useState(false)

  useEffect(() => {
    if (user.s_seven_days_to_endsub !==false || user.is_one_day_to_endsub !==false) {
      setBellActive(true)
    }
  }, [user.s_seven_days_to_endsub, user.is_one_day_to_endsub])

  

    return (
        <section className="header">
          <div className="header__container">
            <label className="header__checkbox">
            <input type="checkbox" className="header__checkbox-input" onChange={switchTheme} checked={theme}></input>
            <span className="checkbox-switch"></span>
          </label>
         
          <button onClick={openNotification} className= "call-button"><span className={bellActive ? " bell bell_active" : "bell"}>{1}</span></button>
          <div className="header_profile-info">
            <img className="profile-info__avatar" src={avatar} alt="аватар пользователя"></img>
            <Link to="/auth"><p className="link header__paragraph">{"Петров Петр"}</p></Link>
          </div>
          </div>
          
        </section>
    )
}

export default connect(changeTheme)(Header);