import { createSlice } from "@reduxjs/toolkit";

export const themeSlice = createSlice({
  name: 'theme',
  initialState: {
    theme: false
  },
  reducers: {
    changeTheme(state) {
      state.theme = !state.theme
    }
  }
})

export const {changeTheme} = themeSlice.actions
export default themeSlice.reducer