import {React, useState,useEffect} from "react";

import './RealtyForm.css';

import ApartmentForm from "./ApartmentForm";
import HouseForm from './HouseForm';
import CommersialForm from "./CommersialForm";
import LandPlotForm from './LandPlotForm';

function Realty({
  cities,
  isOk,
  handleSearchCities,
  page, 
  setPage, 
  handleClose,
  realtyCategory, 
  setRealtyCategory, 
  setActiveCategory
}) {

  // отслеживает выбор категории недвижимости
function handleChoice(e) {
  setRealtyCategory(e.target.name)
  handleNextPage()
}

// переход на следующую страничку попап-формы
function handleNextPage() {
  setPage(page + 1) 
}

// возврат на предыдущую старичку попап-формы
function handleBackPage() {
if (page===3) {
  setRealtyCategory('')
  setPage(page - 3)
} else {
if (page===1) {
  setRealtyCategory('')
}
if (page===0) {
  setActiveCategory('')
}
setPage(page - 1)
}
}

  return(
    <>
    <div className="popup__header">
      <button className={page===3 ? "popup__go-back-button popup__go-back-button_none" : "popup__go-back-button"} onClick={handleBackPage}></button>
      <h3 className="title popup__title">
        <span className={realtyCategory==="commercial"? "card-icon commercial" : realtyCategory==="landPlot"? "card-icon landPlot" : realtyCategory==="house"? "card-icon house": "card-icon realty" }></span>
        {realtyCategory==="apartment" 
          ? "Недвижимость/Квартира"
          : realtyCategory==="commercial" 
          ? "Недвижимость/Коммерческая" 
          : realtyCategory==='landPlot'
          ? "Недвижимость/Участок"
          : realtyCategory==='house'
          ? "Недвижимость/Дом"
          : "Недвижимость"
        }
      </h3>
      <p className="paragraph popup__paragraph">
        Заполнение всех данных поможет нам точнее рассчитать&nbsp;все показатели
      </p>
      <button className="close-button" onClick={handleClose}></button>
    </div> 

   {
    
     realtyCategory === "apartment" 
      ? <ApartmentForm 
      isOk={isOk}
      handleBackPage={handleBackPage}
      handleNextPage={handleNextPage}
    
      cities = {cities}
      handleSearchCities = {handleSearchCities}
      page = {page}
      setPage = {setPage}
      handleClose = {handleClose}
      realtyCategory = {realtyCategory}
      setRealtyCategory = {setRealtyCategory}
      setActiveCategory = {setActiveCategory}/> 

      : realtyCategory === 'commercial'
        ? <CommersialForm 
        isOk={isOk}
        handleBackPage={handleBackPage}
        handleNextPage={handleNextPage}
      
        cities = {cities}
        handleSearchCities = {handleSearchCities}
        page = {page}
        setPage = {setPage}
        handleClose = {handleClose}
        realtyCategory = {realtyCategory}
        setRealtyCategory = {setRealtyCategory}
        setActiveCategory = {setActiveCategory}/> 

        : realtyCategory==='house'
          ? <HouseForm
          isOk={isOk}
          handleBackPage={handleBackPage}
          handleNextPage={handleNextPage}
        
          cities = {cities}
          handleSearchCities = {handleSearchCities}
          page = {page}
          setPage = {setPage}
          handleClose = {handleClose}
          realtyCategory = {realtyCategory}
          setRealtyCategory = {setRealtyCategory}
          setActiveCategory = {setActiveCategory}/> 

          : realtyCategory==='landPlot'
              ? <LandPlotForm
              isOk={isOk}
              handleBackPage={handleBackPage}
              handleNextPage={handleNextPage}
            
              cities = {cities}
              handleSearchCities = {handleSearchCities}
              page = {page}
              setPage = {setPage}
              handleClose = {handleClose}
              realtyCategory = {realtyCategory}
              setRealtyCategory = {setRealtyCategory}
              setActiveCategory = {setActiveCategory}/> 
              : 
              <div className="category">
                <button className="button category-button" name="apartment" onClick={handleChoice}><span className="card-icon realty"></span>Квартира<span className="category__button-icon"></span></button>
                <button className="button category-button" name="house" onClick={handleChoice}><span className="card-icon house"></span>Дом<span className="category__button-icon"></span></button>
                <button className="button category-button" name="commercial" onClick={handleChoice}><span className="card-icon commercial"></span>Коммерческая<span className="category__button-icon"></span></button>
                <button className="button category-button" name="landPlot" onClick={handleChoice}><span className="card-icon landPlot"></span>Участок<span className="category__button-icon"></span></button>
              </div>
    } 

    </>
  )
}

export default Realty;

