import "./DepositEdit.css"

import {React, useEffect, useState} from "react";
import DatePicker, {registerLocale} from "react-datepicker";
// 
import ru from "date-fns/locale/ru";

import {
    dropOptionsBank,
    emptyFieldErrorMessage,
    negativeErrorMessage,
    dropOptionsСurrency,
    greatValue,
    dropOptionsMetal, URL,
} from "../../../../utils/constants";
import {
    createDepositReplenishment,
    createDepositWithdrawal,
    createMetalReplenishment,
    createMetalWithdrawal,
    deleteDepositReplenishment,
    deleteDepositWithdrawal,
    deleteMetalReplenishment,
    deleteMetalWithdrawal,
    updateDeposit,
    updateMetals
} from "../../../../store/reducers/RocetSlice";
import {useDispatch, useSelector} from "react-redux";


function DepositEdit({
           handleClose,
           isOpened,
           depositCategory,
       }) {

    const dispatch = useDispatch();

    const depositItem =  useSelector((state) => state.rocketSlice.bank_deposits.objects[state.rocketSlice.activeId]);
    const metalItem = useSelector((state) => state.rocketSlice.metal_accounts.objects[state.rocketSlice.activeId]);
    const [isChecked, setIsChecked] = useState(true)
    const [edit, setEdit] = useState(false)
    const [addOperation, setAddOperation] = useState(false)
    const [openModal, setOpenModal] = useState(false)
    const [isSection, setSection] = useState("info")

    const currentsName = dropOptionsСurrency.filter(current => current.value === depositItem.currency)
    const metalsName = dropOptionsMetal.filter(metal => metal.type === metalItem.type_metal)

    const [isValidOne, setIsValidOne] = useState(false)
    const [isValidTwo, setIsValidTwo] = useState(false)

    const [radioFirst, setRadioFirst] = useState(false);

    const [startDate, setStartDate] = useState(new Date());
    const dateName = `${startDate.getDate().toString().padStart(2,'0')}.${(startDate.getMonth()+1).toString().padStart(2,'0')}.${startDate.getFullYear()}`

    const [endDate, setEndDate] = useState(new Date());
    const [dateDeposit, setDate] = useState(new Date());
    registerLocale('ru', ru)



    const [depositMetal, setDepositMetal] = useState({
        bank: '',
        startDate: '',
        startWeight: '',
        endWeight: '',
        startPrice: '',

        endPrice: 12000000,
        income: 5,
        profit: 1000000,
    })

    const [depositMetalErrors, setDepositMetalErrors] = useState({
        bank: null,
        startDate: '',
        startWeight: null,
        endWeight: null,
        startPrice: null,
    })
////////////////////////////////////////////////////////////////////////////////////////

    const [depositMoney, setDepositMoney] = useState({
        bank: '',
        startSum: '',
        wallet: '',
        rate: '',
        startDate: '',
        endDate: '',

        endPrice: 12000000,
        income: 5,
        profit: 1000000,
    })

    const [depositMoneyErrors, setDepositMoneyErrors] = useState({
        bank: null,
        startSum: null,
        wallet: null,
        rate: null,
        startDate: '',
        endDate: '',
    })
////////////////////////////////////////////////////////////////////////////////////////

    const [historyMetal, setHistoryMetal] = useState({
        coldType: !radioFirst,
        hotType: radioFirst,
        dateDeposit: '',
        weight: '',
        sum: '',
    })


    const [historyMetalError, setHistoryMetalError] = useState({
        dateDeposit: '',
        weight: null,
        sum: null,
    })
////////////////////////////////////////////////////////////////////////////////////////

    const [historyMoney, setHistoryMoney] = useState({
        coldType: !radioFirst,
        hotType: radioFirst,
        dateDeposit: '',
        sum: '',
    })

    const [historyMoneyErrors, setHistoryMoneyErrors] = useState({
        dateDeposit: '',
        sum: null,
    })

    function MyComponent() {
        async function fetchData() {
            try {
                if (depositCategory === 'metal') {
                    await console.log()
                    setDepositMetal({
                        bank: metalItem.bank,
                        startWeight: metalItem.start_weight,
                        endWeight: metalItem.current_weight,//
                        startPrice: metalItem.start_price,
                        startDate: new Date(metalItem.start_date),

                        endPrice: metalItem.statistic[0] === undefined ? metalItem.current_price :
                        metalItem.statistic[0].current_price,//
                        income: metalItem.statistic[0] === undefined ? 0 :
                        metalItem.statistic[0].profit_percentage,
                        profit: metalItem.statistic[0] === undefined ? 0 :
                        metalItem.statistic[0].profit_amount,

                        isStatistic: metalItem.display_in_general
                    });
                    setDepositMetalErrors({
                        bank: '',
                        startDate: '',
                        startWeight: '',
                        endWeight: '',
                        startPrice: '',
                    });
                    setStartDate(new Date(metalItem.start_date))
                }
                if (depositCategory === 'money'){
                    await console.log()

                    setDepositMoney({
                        bank: depositItem.bank,
                        startSum: Number(depositItem.start_amount),
                        wallet: depositItem.currency,
                        rate: depositItem.interest_rate,
                        startDate: new Date(depositItem.start_date),
                        endDate: new Date(depositItem.end_date),

                        endPrice: depositItem.current_sum,//
                        income: depositItem.profit_percentage,
                        profit: depositItem.profit_amount,

                        isStatistic: depositItem.display_in_general
                    })
                    setDepositMoneyErrors({
                        bank: '',
                        startSum: '',
                        wallet: '',
                        rate: '',
                        startDate: '',
                        endDate: '',
                    })
                    setStartDate(new Date(depositItem.start_date))
                    setEndDate(new Date(depositItem.end_date))
                }
            } catch (error) {
                console.log(error)
            }
        }

        fetchData();
    }
    useEffect(() => {
        MyComponent();
    }, [depositItem, metalItem])

    const depositRefills = depositCategory === "money" ?
        depositItem.refills
        :
        metalItem.refills
    const depositWithdrawals =depositCategory === "money" ?
        depositItem.withdrawals
        :
        metalItem.withdrawals


    function getOperations () {
        const result = []

        const depositOpetations = [...depositRefills,...depositWithdrawals].sort(function(a,b) {
            return new Date(b.refill_date) - new Date(a.withdrawal_date);
        });

        depositOpetations.forEach((refill, index) => {

            let parseDate = new Date(refill.refill_date || refill.withdrawal_date)
            let parseMouth = parseDate.getMonth() + 1
            let operationsDate = `${parseDate.getDate().toString().padStart(2,"0")}.${parseMouth.toString().padStart(2,"0")}.${parseDate.getFullYear()}`
            result.push(

                <div className="story__operations-id" key={index}>
                    <div className="story__operations-data w-20">
                        <p className="paragraph_s">{operationsDate}</p>
                    </div>
                    { depositCategory === 'metal' ?
                        <>
                            <div className="story__operations-operation w-45">
                                <p className="paragraph_s">{refill.refill_amount ? "Покупка" : "Продажа" }</p>
                            </div>
                            <div className="story__operations-value w-25">
                                <p className="paragraph_s">{`${Number(refill.refill_amount ||  refill.withdrawal_amount)} ₽ / ${refill.refill_weight} гр.`}</p>
                            </div>
                        </>
                        :
                        <>
                            <div className="story__operations-operation w-60">
                                <p className="paragraph_s">{refill.refill_amount ? "Покупка" : "Продажа" }</p>
                            </div>
                            <div className="story__operations-value w-15">
                                <p className="paragraph_s">{`${Number(refill.refill_amount ||  refill.withdrawal_amount)} ₽`}</p>
                            </div>
                        </>
                    }
                    <div className="story__operations-delete w-10">
                        {depositCategory === 'metal' ?
                            <svg onClick={() => {

                                    if (refill.refill_amount) {
                                        dispatch(deleteMetalReplenishment(metalItem.id, refill.id))
                                    } else {
                                        dispatch(deleteMetalWithdrawal(metalItem.id, refill.id))
                                    }
                                }
                            }
                                 className="delete-operations" width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path d="M4.2417 7.79102L5.775 16.1993C5.90834 16.9493 6.56667 17.4993 7.33333 17.4993H12.6583C13.425 17.4993 14.0833 16.9577 14.2167 16.1993L15.75 7.79102" stroke="#A6A6A6" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
                                <path d="M2.5249 5.15039H17.5249" stroke="#A6A6A6" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
                                <path d="M6.0166 5.14948L7.24163 2.91615C7.38329 2.66615 7.64998 2.50781 7.93331 2.50781H12.0666C12.3583 2.50781 12.625 2.66615 12.7583 2.91615L13.9833 5.14948" stroke="#A6A6A6" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
                                <path d="M11.6747 10.832H8.32471" stroke="#A6A6A6" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
                            </svg>
                            :
                            <svg onClick={() => {

                                if (refill.refill_amount) {
                                    dispatch(deleteDepositReplenishment(depositItem.id, refill.id))
                                } else {
                                    dispatch(deleteDepositWithdrawal(depositItem.id, refill.id))
                                }
                            }
                            } className="delete-operations" width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path d="M4.2417 7.79102L5.775 16.1993C5.90834 16.9493 6.56667 17.4993 7.33333 17.4993H12.6583C13.425 17.4993 14.0833 16.9577 14.2167 16.1993L15.75 7.79102" stroke="#A6A6A6" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
                                <path d="M2.5249 5.15039H17.5249" stroke="#A6A6A6" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
                                <path d="M6.0166 5.14948L7.24163 2.91615C7.38329 2.66615 7.64998 2.50781 7.93331 2.50781H12.0666C12.3583 2.50781 12.625 2.66615 12.7583 2.91615L13.9833 5.14948" stroke="#A6A6A6" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
                                <path d="M11.6747 10.832H8.32471" stroke="#A6A6A6" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
                            </svg>


                        }
                    </div>
                </div>
            )
        });

        return result
    }

    useEffect(() => {
        getOperations()
    }, [depositRefills,depositWithdrawals]);
////////////////////////////////////////////////////////////////////////////////////////

    // Отзычивая валидность формы редактирования вклада
    useEffect(() => {
        if (depositCategory === 'metal') {
            setIsValidOne(!Object.values(depositMetalErrors).some(x => x !== ''))
            setIsValidTwo(!Object.values(historyMetalError).some(x => x !== ''))
        }
        else if (depositCategory === 'money') {
            setIsValidOne(!Object.values(depositMoneyErrors).some(x => x !== ''))
            setIsValidTwo(!Object.values(historyMoneyErrors).some(x => x !== ''))
        }
    }, [depositMetalErrors, historyMetalError,depositMoneyErrors,historyMoneyErrors ])
////////////////////////////////////////////////////////////////////////////////////////
    // добавляет в объект полей значения специфических инпутов при их изменении
    useEffect(() => {
        if (depositCategory === 'metal'){
            setDepositMetal({...depositMetal,
                startDate: new Date(metalItem.start_date),
                isStatistic: isChecked,
            });

            setHistoryMetal({...historyMetal,
                dateDeposit: new Date(),
            });
        }
        else if (depositCategory === 'money'){
            setDepositMoney({...depositMoney,
                startDate: new Date(depositItem.start_date),
                endDate: new Date(depositItem.end_date),
                isStatistic: isChecked,
            });

            setHistoryMoney({...historyMoney,
                dateDeposit: new Date(),
            });
        }
    }, [isChecked, dateDeposit,])
////////////////////////////////////////////////////////////////////////////////////////
// обычный инпут
    function handleChangeInput(e) {
        const {name, value} = e.target;
        if (depositCategory === 'metal'){
            if (isSection === 'info'){
                setDepositMetal({
                    ...depositMetal,
                    [name]: value
                })

                value === ''
                    ? setDepositMetalErrors({...depositMetalErrors, [name]: emptyFieldErrorMessage})
                    :  e.target.value <= 0
                        ? setDepositMetalErrors({...depositMetalErrors, [name]: negativeErrorMessage})
                        : setDepositMetalErrors({...depositMetalErrors, [name]: ''})

                if (name === 'startWeight' && value > 2147483647) {
                    setDepositMetalErrors({...depositMetalErrors, [name]: greatValue})
                }
                if (name === 'startPrice' && value >= 1000000000000000000) {
                    setDepositMetalErrors({...depositMetalErrors, [name]: greatValue})
                }
            }
            else if (isSection === 'story'){
                setHistoryMetal({
                    ...historyMetal,
                    [name]: value
                })

                value === ''
                    ? setHistoryMetalError({...historyMetalError, [name]: emptyFieldErrorMessage})
                    :  e.target.value <= 0
                        ? setHistoryMetalError({...historyMetalError, [name]: negativeErrorMessage})
                        : setHistoryMetalError({...historyMetalError, [name]: ''})
            }
        }

        else if (depositCategory === 'money'){
            if (isSection === 'info') {
                setDepositMoney({
                    ...depositMoney,
                    [name]: value
                })

                value === ''
                    ? setDepositMoneyErrors({...depositMoneyErrors, [name]: emptyFieldErrorMessage})
                    :  e.target.value <= 0
                        ? setDepositMoneyErrors({...depositMoneyErrors, [name]: negativeErrorMessage})
                        : setDepositMoneyErrors({...depositMoneyErrors, [name]: ''})

                if (name === 'startSum' && value >= 1000000000000000000) {
                    setDepositMoneyErrors({...depositMoneyErrors, [name]: greatValue})
                }
                if (name === 'rate' && value > 1000) {
                    setDepositMoneyErrors({...depositMoneyErrors, [name]: greatValue})
                }
            }
            else if (isSection === 'story'){
                setHistoryMoney({
                    ...historyMoney,
                    [name]: value
                })

                value === ''
                    ? setHistoryMoneyErrors({...historyMoneyErrors, [name]: emptyFieldErrorMessage})
                    :  e.target.value <= 0
                        ? setHistoryMoneyErrors({...historyMoneyErrors, [name]: negativeErrorMessage})
                        : setHistoryMoneyErrors({...historyMoneyErrors, [name]: ''})
            }
        }
    }

    function handleChangeBankDrop(value) {
        if (depositCategory === 'metal'){
            setIsOptionBankDown(false)
            setDepositMetal({...depositMetal, 'bank': value})
            setDepositMetalErrors({...depositMetalErrors, 'bank': ''})
        }
        else if (depositCategory === 'money'){
            setIsOptionBankDown(false)
            setDepositMoney({...depositMoney, 'bank': value})
            setDepositMoneyErrors({...depositMoneyErrors, 'bank': ''})
        }
    }
    function handleChangeWalletDrop(value) {
        setIsOptionСurrencyDown(false)
        setDepositMoney({...depositMoney, 'wallet': value})
        setDepositMoneyErrors({...depositMoneyErrors, 'wallet': ''})
    }


    const [isOptionBankDown, setIsOptionBankDown] = useState(false);
    const [isOptionСurrencyDown, setIsOptionСurrencyDown] = useState(false);


    function getModel () {
        setOpenModal(!openModal)
    }

    function handleChangeFirstFloorRadio() {
        setRadioFirst(!radioFirst)
        console.log(radioFirst)
    }

    function createAddOperation () {
        setAddOperation(!addOperation)
    }

    function handleChangeCheckbox() {
        setIsChecked(!isChecked)
    }

    function handleSubmitOperations (e) {
        e.preventDefault();

        if (depositCategory === 'money'){
            if (radioFirst){
                dispatch(createDepositWithdrawal(depositItem.id, historyMoney))
            }else if (!radioFirst){
               dispatch(createDepositReplenishment(depositItem.id, historyMoney))
            }
        }

        else if (depositCategory === 'metal'){
            if (!radioFirst){
                dispatch(createMetalWithdrawal(metalItem.id, historyMetal))
            }else if (radioFirst){
                dispatch(createMetalReplenishment(metalItem.id, historyMetal))
            }
        }
    }

    function handleSubmit(e) {
        e.preventDefault();
        console.log(1)
        if (depositCategory === 'money'){
            dispatch(updateDeposit(depositItem.id, depositMoney))
        }else if(depositCategory === 'metal') {
            dispatch(updateMetals(metalItem.id, depositMetal))
        }
    }

    function updateEdit () {
        setEdit(!edit)
    }
    return (
        <div className="edit edit-deposit">
            <div className={isOpened ? "popup popup_opened" : "popup" }>
                <div className="popup__container__transport-edit popup__container-edit">
                    <div className="popup__header">
                        <button className="popup__go-back-button"></button>
                        { depositCategory === "metal" ?
                            <h3 className="title popup__title">
                                <span className={"card-icon deposit"}></span>
                                {metalsName[0].name}
                            </h3>
                            : depositCategory === "money" ?
                            <h3 className="title popup__title">
                                <span className={"card-icon deposit"}></span>
                                {currentsName[0].name}
                            </h3>
                                : ""
                        }
                        <button className="close-button" onClick={handleClose}></button>
                    </div>

                    { depositCategory === "money" ?
                        <div className="edit-chart">
                            <div className="edit-chart-box transport__edit__chart">
                                <p className="paragraph_l all-no-margin">{depositMoney.endPrice} ₽</p>
                                <p className="paragraph_m text-green all-no-margin">{`↑ ${depositMoney.profit === null ? 0 : depositMoney.profit} (${depositMoney.income === null ? 0 : depositMoney.income }%)`}</p>
                            </div>
                        </div>
                        :
                        <div className="edit-chart">
                            <div className="edit-chart-box transport__edit__chart">
                                <p className="paragraph_l all-no-margin">{depositMetal.endPrice}</p>
                                <p className="paragraph_m text-green all-no-margin">{`↑ ${depositMetal.profit} (${depositMetal.income}%)`}</p>
                            </div>
                        </div>
                    }

                    <div className="edit-form">
                        <div className="edit-on" onClick={getModel}>
                            <div className={openModal === false ? "edit-on-button" : "edit-on-button display-block"} onClick={updateEdit}>
                                <p className="paragraph button-edit button-top">{edit === true ? "Отмена" : "Редактировать"}</p>
                                <p className="paragraph button-edit button-bottom">Удалить</p>
                            </div>
                        </div>
                        <details className="drop-edit" open>
                            <summary className="paragraph drop-edit-text">{dateName}</summary>

                            <div className="deposit__switch">
                                <div className={isSection === "info" ? "deposit__switch-header active-switch" : "deposit__switch-header"} id="info" onClick={() => {setSection("info")}}>
                                    <h3 className={isSection === "info" ? "title title_deposit active-title" : "title title_deposit"}>Информация по вкладу</h3>
                                </div>

                                <div className={isSection === "story" ? "deposit__switch-header active-switch" : "deposit__switch-header"} id="story" onClick={() => {setSection("story")}}>
                                    <h3 className={isSection === "story" ? "title title_deposit active-title" : "title title_deposit"}>История операций</h3>
                                </div>
                            </div>

                            {isSection === "info" && depositCategory === 'metal' ?
                                (<form name="realty-residential" className={edit === true ? "form popup__form__deposit__active-edit" :"form popup__form__deposit__disabled-edit" } onSubmit={handleSubmit}>
                                <fieldset className="popup-fildset inputs-fildset">

                                    <div className='input-dropdown-box'>
                                        <label className="active-form__label"> Банк
                                            <input type="text" className={edit === false ? "form__input input-disabled" : "form__input bg-white-light"} placeholder="ВТБ" name='bank' value={depositMetal.bank} disabled={!edit} onClick={(e) => {setIsOptionBankDown(!isOptionBankDown)}}></input>
                                            <span className = "form__item-error bg-gray-edit">{depositMetalErrors.bank}</span>
                                        </label>
                                        <div className={!isOptionBankDown ? 'dropdown': 'dropdown dropdown_open'} >
                                            {dropOptionsBank.map((item) => {
                                                return <div className="option" key={item.name} onClick={(e) => {handleChangeBankDrop(item.name)}}>{item.name}</div>
                                            })}
                                        </div>
                                    </div>
                                        <>
                                            <label className="active-form__label date-label"> Дата покупки *

                                                <DatePicker
                                                    selected={startDate}
                                                    onChange={setStartDate}
                                                    name="startDate"

                                                    locale='ru'
                                                    dateFormat={'dd.MM.yyyy'}
                                                    calendarStartDay={1}

                                                    showYearDropdown
                                                    showMonthDropdown
                                                    inputType='number'
                                                    disabled={!edit}
                                                />
                                            </label>

                                            <div className="form-flex-wraper">
                                                <label className="active-form__label flex-label"> Начальный вес, гр
                                                    <input type="number" className={edit === false ? "form__input input-disabled" : "form__input bg-white"} placeholder="10" name='startWeight' value={depositMetal.startWeight} disabled></input>
                                                </label>

                                                <label className="active-form__label flex-label"> Текущий вес, гр
                                                    <input type="number" className={edit === false ? "form__input input-disabled" : "form__input bg-white"} placeholder="10" name='endWeight' value={depositMetal.endWeight} disabled onChange={handleChangeInput}></input>
                                                    <span className = "form__item-error bg-gray-edit">{depositMetalErrors.endWeight}</span>
                                                </label>
                                            </div>

                                            <div className="form-flex-wraper">
                                                <label className="active-form__label flex-label"> Начальная цена
                                                    <input type="number" className={edit === false ? "form__input input-disabled" : "form__input bg-white"} placeholder="10 000 000 ₽" name='startPrice' value={depositMetal.startPrice} disabled></input>
                                                </label>

                                                <label className="active-form__label flex-label"> Текущая цена
                                                    <input type="text" className={edit === false ? "form__input input-disabled" : "form__input bg-white"} placeholder="11 000 000 ₽" name='endPrice' value={depositMetal.endPrice + ' ' + '₽'} disabled></input>
                                                </label>
                                            </div>

                                            <div className="form-flex-wraper">
                                                <label className="active-form__label flex-label"> Доходность
                                                    <input type="text" className={edit === false ? "form__input input-disabled" : "form__input bg-white"} placeholder="+ 5%" name='income' value={'+' + ' ' + depositMetal.income + '%'} disabled></input>
                                                </label>

                                                <label className="active-form__label flex-label"> Прибыль
                                                    <input type="text" className={edit === false ? "form__input input-disabled" : "form__input bg-white"} placeholder="1 000 000 ₽" name='profit' value={depositMetal.profit + ' ' + '₽'} disabled></input>
                                                </label>
                                            </div>
                                        </>
                                </fieldset>
                                    <div className="button__edit-box box-deposit">
                                        <label className="checkbox center checkbox-edit" >
                                            <input type="checkbox" className="checkbox-input" checked={depositMetal.isStatistic} onChange={handleChangeCheckbox}></input>
                                            <span className='checkbox-switch'></span>
                                            <span className='checkbox-text'>Отображать в общей статистике</span>
                                        </label>
                                        <button type="submit" className={edit === true ? "button orange-button button-custom" : "orange-button button-disabled"} disabled={!isValidOne} onClick={updateEdit}>Сохранить изменения</button>
                                        <div className={edit === false ? "deposit__footer" : "deposit__footer-none"}>
                                            <span className="paragraph deposit__footer-text">Если вы пополнили вклад или сняли средства, добавьте  эту операцию в разделе «История операций» для более точного расчета</span>
                                        </div>
                                    </div>
                            </form>)

                                : (isSection === "info" && depositCategory === 'money') ?

                                (<form name="realty-residential" className={edit === true ? "form popup__form__deposit__active-edit" :"form popup__form__deposit__disabled-edit" } onSubmit={handleSubmit}>
                                    <fieldset className="popup-fildset inputs-fildset">

                                        <div className='input-dropdown-box'>
                                            <label className="active-form__label"> Банк
                                                <input type="text" className={edit === false ? "form__input input-disabled" : "form__input bg-white-light"} placeholder="ВТБ" name='bank' value={depositMoney.bank} disabled={!edit} onClick={(e) => {setIsOptionBankDown(!isOptionBankDown)}}></input>
                                                <span className = "form__item-error bg-gray-edit">{depositMoneyErrors.bank}</span>
                                            </label>
                                            <div className={!isOptionBankDown ? 'dropdown': 'dropdown dropdown_open'} >
                                                {dropOptionsBank.map((item) => {
                                                    return <div className="option" key={item.name} onClick={(e) => {handleChangeBankDrop(item.name)}}>{item.name}</div>
                                                })}
                                            </div>
                                        </div>
                                        <div className="form-flex-wraper">
                                            <label className="active-form__label flex-label"> Начальная сумма
                                                <input type="number" className={edit === false ? "form__input input-disabled" : "form__input bg-white"} placeholder="50 000" name='startSum' disabled value={depositMoney.startSum}></input>
                                            </label>

                                            <div className='input-dropdown-box'>
                                                <label className="active-form__label flex-label"> Валюта
                                                    <input type="text" className={edit === false ? "form__input input-disabled" : "form__input bg-white-light"} placeholder="RUB (₽)" name='wallet' disabled={!edit} value={depositMoney.wallet} onClick={(e) => {setIsOptionСurrencyDown(!isOptionСurrencyDown)}}></input>
                                                    <span className = "form__item-error bg-gray-edit">{depositMoneyErrors.wallet}</span>
                                                </label>
                                                <div className={!isOptionСurrencyDown ? 'dropdown': 'dropdown dropdown_open'} >
                                                    {dropOptionsСurrency.map((item) => {
                                                        return <div className="option" key={item.name} onClick={(e) => {handleChangeWalletDrop(item.name)}}>{item.name}</div>
                                                    })}
                                                </div>
                                            </div>
                                        </div>

                                            <label className="active-form__label"> Годовой процент, %
                                                <input type="number" className={edit === false ? "form__input input-disabled" : "form__input bg-white-light"} placeholder="8" name='rate' disabled={!edit} value={depositMoney.rate} onChange={handleChangeInput}></input>
                                                <span className = "form__item-error bg-gray-edit">{depositMoneyErrors.rate}</span>
                                            </label>

                                        <div className="form-flex-wraper">
                                            <label className="active-form__label date-label flex-label"> Дата открытия вклада
                                                <DatePicker
                                                    selected={startDate}
                                                    onChange={setStartDate}
                                                    name="startDate"

                                                    locale='ru'
                                                    dateFormat={'dd.MM.yyyy'}
                                                    calendarStartDay={1}

                                                    showYearDropdown
                                                    showMonthDropdown
                                                    inputType='number'
                                                    disabled={!edit}
                                                />
                                            </label>
                                            <label className="active-form__label date-label flex-label"> Дата закрытия вклада
                                                <DatePicker
                                                    selected={endDate}
                                                    onChange={setEndDate}
                                                    name="endDate"

                                                    locale='ru'
                                                    dateFormat={'dd.MM.yyyy'}
                                                    calendarStartDay={1}

                                                    showYearDropdown
                                                    showMonthDropdown
                                                    inputType='number'
                                                    disabled={!edit}
                                                />
                                            </label>
                                        </div>
                                            <label className="active-form__label"> Текущая сумма
                                                <input type="number" className={edit === false ? "form__input input-disabled" : "form__input bg-white"} placeholder="55 000 ₽" name='endPrice' min={0} max={1000000000000000000} disabled value={depositMoney.endPrice} onChange={handleChangeInput}></input>
                                                <span className = "form__item-error bg-gray-edit">{depositMoneyErrors.endPrice}</span>
                                            </label>
                                    </fieldset>
                                    <div className="button__edit-box box-deposit">
                                        <label className="checkbox center checkbox-edit" >
                                            <input type="checkbox" className="checkbox-input" checked={depositMoney.isStatistic} onChange={handleChangeCheckbox}></input>
                                            <span className='checkbox-switch'></span>
                                            <span className='checkbox-text'>Отображать в общей статистике</span>
                                        </label>
                                        <button type="submit" className={edit === true ? "button orange-button button-custom" : "orange-button button-disabled"} disabled={!isValidOne} onClick={updateEdit}>Сохранить изменения</button>
                                        <div className={edit === false ? "deposit__footer" : "deposit__footer-none"}>
                                            <span className="paragraph deposit__footer-text">Если вы пополнили вклад или сняли средства, добавьте  эту операцию в разделе «История операций» для более точного расчета</span>
                                        </div>
                                    </div>
                                </form>)

                                : isSection === "story" && addOperation === false ?

                                    <div>{ depositCategory === 'metal' ?
                                        (metalItem.withdrawals.length === 0 && metalItem.refills.length === 0 ?
                                        <div className="deposit__add">
                                            <span className="paragraph title deposit__add-text">Здесь появится ваша история операций, после того, как вы ее добавите</span>
                                            <button className="button white-button add-operation" onClick={createAddOperation}>+ Добавить операцию</button>
                                        </div>
                                        :
                                        <>
                                            <div className="story__operations">
                                                {getOperations()}
                                            </div>

                                            <button className="button white-button add-operation" onClick={createAddOperation}>+ Добавить операцию</button>
                                        </>
                                        )
                                        :
                                        (depositItem.withdrawals.length === 0 && depositItem.refills.length === 0 ?
                                                <div className="deposit__add">
                                                    <span className="paragraph title deposit__add-text">Здесь появится ваша история операций, после того, как вы ее добавите</span>
                                                    <button className="button white-button add-operation" onClick={createAddOperation}>+ Добавить операцию</button>
                                                </div>
                                                :
                                                <>
                                                    <div className="story__operations">
                                                        {getOperations()}
                                                    </div>

                                                    <button className="button white-button add-operation" onClick={createAddOperation}>+ Добавить операцию</button>
                                                </>
                                        )
                                    }
                                    </div>

                                    : isSection === "story" && addOperation === true && depositCategory === 'metal' ?

                                        <form name="realty-residential-operatins" className="form deposit__add" onSubmit={handleSubmitOperations}>
                                            <fieldset className="popup-fildset inputs-fildset">

                                                <label className="active-form__label date-label margin-no-bottom"> Тип операции
                                                    <div className="radio-wraper radio-checkbox-wrapper margin-top">
                                                        <input
                                                            type="checkbox"
                                                            id="first"
                                                            name="first"
                                                            className="checkbox radio-checkbox"
                                                            checked={!radioFirst}
                                                            onClick={handleChangeFirstFloorRadio}
                                                        />
                                                        <label className="active-form__label radio-checkbox-label" htmlFor="first">Пополнение (покупка)</label>
                                                        <input
                                                            type="checkbox"
                                                            id="last"
                                                            name="last"
                                                            className="checkbox radio-checkbox"
                                                            checked={radioFirst}
                                                            onClick={handleChangeFirstFloorRadio}
                                                        />
                                                        <label className="active-form__label radio-checkbox-label" htmlFor="last">Снятие (продажа)</label>
                                                    </div>
                                                </label>

                                                <label className="active-form__label date-label"> Дата *
                                                    <DatePicker
                                                        selected={dateDeposit}
                                                        onChange={setDate}
                                                        name="dateDeposit"

                                                        locale='ru'
                                                        dateFormat={'dd.MM.yyyy'}
                                                        calendarStartDay={1}

                                                        showYearDropdown
                                                        showMonthDropdown
                                                        inputType='number'

                                                    />
                                                </label>

                                                <label className="active-form__label">Вес, гр
                                                    <input type="text" className= "form__input bg-white-light" placeholder="Placeholder" name='weight' value={historyMetal.weight} onChange={handleChangeInput}></input>
                                                    <span className = "form__item-error bg-gray-edit">{historyMetalError.weight}</span>
                                                </label>

                                                <label className="active-form__label">Сумма
                                                    <input type="number" className="form__input bg-white-light" placeholder="10 000" name='sum' value={historyMetal.sum} onChange={handleChangeInput}></input>
                                                    <span className = "form__item-error bg-gray-edit">{historyMetalError.sum}</span>
                                                </label>

                                                <div className="form-flex-wraper margin-top">
                                                    <button type="button" className="white-button button button-mx" onClick={createAddOperation}>{"Закрыть"}</button>
                                                    <button disabled={!isValidTwo} className="orange-button button button-mx">{"Сохранить"}</button>
                                                </div>
                                            </fieldset>
                                        </form>

                                    : (isSection === "story" && addOperation === true && depositCategory === 'money') ?

                                        <form name="realty-residential-operatins" className="form popup__form deposit__add" onSubmit={handleSubmitOperations}>
                                            <fieldset className="popup-fildset inputs-fildset">

                                                <label className="active-form__label date-label margin-no-bottom"> Тип операции
                                                    <div className="radio-wraper radio-checkbox-wrapper margin-top">
                                                        <input
                                                            type="checkbox"
                                                            id="first"
                                                            name="first"
                                                            className="checkbox radio-checkbox"
                                                            checked={!radioFirst}
                                                            onClick={handleChangeFirstFloorRadio}
                                                        />
                                                        <label className="active-form__label radio-checkbox-label" htmlFor="first">Пополнение (покупка)</label>
                                                        <input
                                                            type="checkbox"
                                                            id="last"
                                                            name="last"
                                                            className="checkbox radio-checkbox"
                                                            checked={radioFirst}
                                                            onClick={handleChangeFirstFloorRadio}
                                                        />
                                                        <label className="active-form__label radio-checkbox-label" htmlFor="last">Снятие (продажа)</label>
                                                    </div>
                                                </label>

                                                <label className="active-form__label date-label"> Дата *
                                                    <DatePicker
                                                        selected={dateDeposit}
                                                        onChange={setDate}

                                                        locale='ru'
                                                        dateFormat={'dd.MM.yyyy'}
                                                        calendarStartDay={1}

                                                        showYearDropdown
                                                        showMonthDropdown
                                                        inputType='number'

                                                    />
                                                </label>

                                                <label className="active-form__label">Сумма
                                                    <input type="number" className="form__input bg-white-light" placeholder="10 000" name='sum' value={historyMoney.sum} onChange={handleChangeInput}></input>
                                                    <span className = "form__item-error bg-grey-edit">{historyMoneyErrors.sum}</span>
                                                </label>

                                                <div className="form-flex-wraper margin-top">
                                                    <button type="button" className="white-button button button-mx" onClick={createAddOperation}>{"Закрыть"}</button>
                                                    <button type="submit" disabled={!isValidTwo} className="orange-button button button-mx">Сохранить изменения</button>
                                                </div>
                                            </fieldset>
                                        </form>

                                                : ''
                            }
                        </details>
                    </div>

                </div>
            </div>
        </div>
    )
}

export default DepositEdit