// БАЙКОНУР
export const getRocket = () => {
  return axios({
    url: `${URL}/honeycomb/`,
    method: 'GET',
    headers: {
      "Authorization":`Bearer ${sessionStorage.getItem('token')}`
    }, 
  })
}

 // АВТОРИЗАЦИЯ //
// РЕГИСТРАЦИЯ
export const register = (email, password) => {
  return axios({
    url: `${URL}/auth/users/`,
    method: 'POST',
    data: {
      email: email,
      password: password,
    }
  })
}

// ПОЛУЧЕНИЕ ТОКЕНА (ЛОГИН)
export const createToken = (email, password) => {
  return axios({
    url: `${URL}/auth/jwt/create/`,
    method: 'POST',
    data: {
      email: email,
      password: password,
    }
  })
}


export const changePassword = (password, newPassword) => {
  return axios({
    url: `${URL}/auth/users/set_password/`,
    method: 'POST',
    headers: {
      "Authorization":`Bearer ${sessionStorage.getItem('token')}`
    }, 
    data: {
      new_password: newPassword,
      current_password: password,
    }
  })
}

export const getUser = () => {
  return axios({
    url: `${URL}/auth/users/me/`,
    method: 'GET',
    headers: {
      "Authorization":`Bearer ${sessionStorage.getItem('token')}`
    } 
  })
}

export const patchUser = (user) => {
  return axios({
    url:`${URL}/auth/users/me/`,
    method: 'PATCH',
    headers: {
      "Authorization":`Bearer ${sessionStorage.getItem('token')}`
    },
    data: {
      name: user.name,
      email: user.email,
      phone: user.phone,
    }
  })
}

export const patchUserAgree =(agree) => {
  return axios({
    url:`${URL}/auth/users/me/`,
    method: 'PATCH',
    headers: {
      "Authorization":`Bearer ${sessionStorage.getItem('token')}`
    },
    data: {
      is_push_agree: agree.is_push_agree,
      is_email_agree: agree.is_email_agree,
    }
  })
}

export const deleteUser = () => {
  return axios({
    url: `${URL}/auth/users/me/`,
    method: 'DELETE',
    headers: {
      "Authorization":`Bearer ${sessionStorage.getItem('token')}`
    }
  })
}

