// ДИДЖИТАЛ РУБЛЬ 
export const getDigitalRuble =() => {
  const token = sessionStorage.getItem('token');
  return  axios({
    url:`${URL}/digital_ruble/`,
    method: 'GET',
    headers: {
      "Authorization":`Bearer ${token}`
    },
  })
}

export const postDigitalRuble = (active,dispatch) => {
  return axios({
    url:`${URL}/digital_ruble/`, 
    method: 'POST',
    headers: {
      "Authorization":`Bearer ${sessionStorage.getItem('token')}`
    }, 
    data: {   // с точки зрения axios  это body
      "display_in_general": active.isStatistic,
      "bank": active.bank,
      "start_date": active.date,
      "start_amount": active.sum,
    }
  })
    .then((res) => {
      dispatch(addDigital(res.data))
    })
    .catch((error) => {
      console.log(error)
      return null
    })
}

export function putDigitalRuble (id, form, dispatch)  {
  const token = sessionStorage.getItem('token');
  axios({
    url:`${URL}/digital_ruble/${id}/`,
    method: 'PATCH',
    headers: {
      "Authorization":`Bearer ${token}`
    },
    data: {
      "display_in_general": form.isStatistic,
      "bank": form.bank,
      "start_date": form.date,
      "start_amount": form.sum,
    }
  })
      .then((res) => {
        dispatch(editDigital(res.data))
      })
      .catch((error) => {
        console.log(error)
        return null
      })
}
