export function putPlots(id,form, dispatch) {
    const token = sessionStorage.getItem('token');
    axios({
        url: `${URL}/land_plot/${id}/`,
        method: 'patch',
        headers: {
            "Authorization":`Bearer ${token}`
        },
        data: {
            "display_in_general": form.isStatistic,
            "total_area": form.square,
            "start_date": form.startDate,
            "land_category": form.type.value,
        }
    })
        .then((res) => {
            dispatch(getPlot(id))
        })
        .catch((error) => {
            console.log(error)
        })
}
