// КОШЕЛЬКИ
export const getMarket = (id, dispatch) =>{
    const token = sessionStorage.getItem('token');
    return axios({
        url:`${URL}/markets/${id}/`,
        method: 'GET',
        headers: {
            "Authorization":`Bearer ${token}`
        },
    })
        .then((res) => {
            console.log(res.data)
            if (res.data.market === "BINANCE"){
                dispatch(editBinance(res.data))
            }
            else if (res.data.market === "TINKOFF"){
                dispatch(editTinkoff(res.data))
            }
            else if (res.data.market === "FINAM"){
                dispatch(editFinam(res.data))
            }
        })
        .catch((error) => {
            console.log(error)
            return null
        })
}

