export async function getModels(id) {
  const token = sessionStorage.getItem('token');
  const response = await axios.get(`${URL}/car-brands/${id}/models/`, {
    headers: {
      "Authorization": `Bearer ${token}`
    }
  })
    return response.data
}

export function handleSearchCities(search, dispatch) {
  const token = sessionStorage.getItem('token');
  return axios({
    url: `${URL}/cities/`,
    method: 'GET',
    params: {search: search},
    headers: {
      "Authorization":`Bearer ${token}`
    },
  })
      .then((res) => {
        dispatch(setCity(res.data));
      })
      .catch((error) => {
        console.log(error)
      })
}