import { createSlice } from "@reduxjs/toolkit";

export const loginSwitchSlice = createSlice({
  name: 'loginSwitch',
  initialState: {
    loginSwitch: true
  },
  reducers: {
    changeLoginSwitch(state, action) {
      state.loginSwitch = action.payload
    }
  }
})

export const {changeLoginSwitch} = loginSwitchSlice.actions
export default loginSwitchSlice.reducer