import {React} from "react";

import './NotificationPopup.css'

import { useSelector } from "react-redux";

function Notification ({isOpen, onClose}) {
  
  const user = useSelector((state) => state.userSlice.user)

  // сообщение, сколько дней осталось до окончания подписки
  const days = (user.is_seven_days_to_endsub === true && user.is_one_day_to_endsub !==true ? 'через 7 дней' : 'завтра') 

    const subDate = new Date(user.subscription_end);
    const castomMouth = (subDate.getMonth() + 1).toString().padStart(2,'0');
    const subDateTime = `(${subDate.getDate().toString().padStart(2,'0')}.${castomMouth}.${subDate.getFullYear()} в ${subDate.getHours().toString().padStart(2,'0')}:${subDate.getMinutes().toString().padStart(2,'0')})`;


        return (
            <div className="notification-popup">
                <div className={isOpen ? "popup popup_opened" : "popup"}>
                    <div className="popup__container popup__notification">
                      <button className="close-button notification__close-button" onClick={onClose}></button>
                      <h3 className="notification-title">{`Ваша подписка заканчивается ${days}. ${subDateTime}`}</h3>
                      <p className="paragraph">Подпишитесь на наши уведомления в разделе “Настройки”, и получайте уведомления на электронную почту</p>
                      <button type="button" className="button orange-button button_notification" onClick={onClose}>Продлить подписку</button>
                    </div>
                </div>

            </div>
        )
  }

  export default Notification;