import { createSlice } from "@reduxjs/toolkit";
import { signUp } from "../authActions/register";
import { signIn } from "../authActions/login";
import { registerError, someLoginError } from "../../utils/constants";

const initialState = {
  user: {},
  isLogged: false,
  registerError: '',
  loginError: '',
  loading: false,
}

export const userSlice = createSlice({
  name: 'user',
  initialState,

  reducers: {
  //проверка и вход при обновлении страницы
    log: (state, action) => {
      state.user = action.payload
      state.isLogged = true
      state.loading = true
    },

  // выход из аккаунта
    loginOut: () => initialState,

  // редактирование пользователя
    patchInfo: (state, action) => {
      state.user = action.payload
    }
  },

  extraReducers(builder) {
    builder
    // регистрация
      .addCase(signUp.fulfilled, (state, action) => {
        // state.user = action.payload
        state.registerError = ''
      })
      .addCase(signUp.rejected, (state) => {
        state.registerError = registerError
      })

    // логин
      .addCase(signIn.fulfilled, (state, {payload}) => {
        state.user = payload
        state.isLogged = true
        window.location.replace('/main');
      })
      .addCase(signIn.rejected, (state) => {
        state.loginError = someLoginError
      })
  }
})

export const {log, loginOut, patchInfo} = userSlice.actions
export default userSlice.reducer