import {createSlice} from "@reduxjs/toolkit";
import {getBrands, getModels, getRocket, handleSearchCities} from "../../../api/api";

export const drops = createSlice({
    name:'drops',
    initialState: {
        brand: null,
        model: [],
        city:null,
        loading: false,
        error: null
    },

    reducers: {
        setBrand: (state, action) => {
            state.brand = action.payload
        },
        setModel: (state, action) => {
            state.model = action.payload
        },
        setCity: (state, action) => {
            state.city = [...action.payload]
        },
        setLoading: (state) => {
            state.loading = true;
            state.error = null;
        },
        setError: (state, action) => {
            state.loading = false;
            state.error = action.payload;
        },
    },
});

export const getBrand = () => async (dispatch) => {
    try {
        const response = await getBrands();
        dispatch(setBrand(response.data));
    } catch (error) {
        dispatch(setError(error));
    }
};

export const getModel = (id) => async (dispatch) => {
    try {
        const response = await getModels(id);
        dispatch(setModel(response));
        dispatch(setLoading());
    } catch (error) {
        dispatch(setError(error));
    }
};

export const getCity = (search) => async (dispatch) => {
    try {
         await handleSearchCities(search, dispatch);
    } catch (error) {
        dispatch(setError(error));
    }
};


export const {setBrand, setModel, setCity, setLoading, setError} = drops.actions;

export default drops.reducer