import {React, useState, useEffect} from "react";

import DatePicker, {registerLocale} from "react-datepicker";
import ru from 'date-fns/locale/ru';


import Comfirmation from "../Confirmation/Confirmation";

import {
    dropOptions,
    guestsFromTheFuture,
    calendarErrorMessage,
    emptyFieldErrorMessage,
    negativeErrorMessage,
    choiceErrorMessage, dropOptionsTypeHouse,
} from "../../../utils/constants";

import './RealtyForm.css';
import {useDispatch, useSelector} from "react-redux";
import {getCity} from "../../../store/reducers/Actives/Drops";
import {createVillas} from "../../../store/reducers/RocetSlice";

function HouseForm({
  isOk,
  handleBackPage,
  handleNextPage,

  page, 
  setPage, 
  handleClose,
  realtyCategory, 
  setRealtyCategory, 
  setActiveCategory
}) {

    const dispatch = useDispatch();
    const cities = useSelector((state) => state.drops.city);
  const [isValidOne, setIsValidOne] = useState(false)
  const [isValidTwo, setIsValidTwo] = useState(false)

  const [purchaseDate, setPurchaseDate] =useState(new Date());
  const [statisticCheckboxChecked, setStatisticCheckboxChecked] = useState(true) // состояние чекбокса

  const worldDate = new Date()

///////////////////////////////////////////////////////////////////////////////////////

const [house, setHouse] = useState({
  typeOfHouse: {name: '', value: ''},
  squareHome:'',
  cost:'',
  squareLand:'',

  street:'',
  house: '',
  city:'',
  name: '',
  region: '',

})

const [houseErrorsOne, setHouseErrorsOne] = useState({
    calendar: '',
    squareHome: null,
    squareLand: null,
    cost: null,
})

const [houseErrorsTwo, setHouseErrorsTwo] = useState({
  street: null,
  house: null,
  city: null,
  typeOfHouse: null,
})

///////////////////////////////////////////////////////////////////////////////////////

// добавляет в объект полей значения специфических инпутов при их изменении
useEffect(() => {

    setHouse({... house,
      date: purchaseDate,
      isStatistic: statisticCheckboxChecked,
    })
  },[purchaseDate,
      statisticCheckboxChecked,
    ])


// Отзычивая валидность формы жилой недвижимости
useEffect(() => {

    page === 1
      ? setIsValidOne(!Object.values(houseErrorsOne).some(x => x !== ''))
      :  setIsValidTwo(!Object.values(houseErrorsTwo).some(x => x !== '' )) 
  
}, [houseErrorsOne,
    houseErrorsTwo,
    ])
///////////////////////////////////////////////////////////////////////////////////////

// изменение значений стандартных инпутов на первой страничке формы жилой недвижимости
function handleChangeInput(e) {
  const {name, value} = e.target;
  setHouse({
    ...house,
    [name]: value
  })
  
  if (page === 1) {
    value === ''
    ? setHouseErrorsOne({...houseErrorsOne, [name]: emptyFieldErrorMessage})
    :  e.target.value <= 0 
      ? setHouseErrorsOne({...houseErrorsOne, [name]: negativeErrorMessage})
      : setHouseErrorsOne({...houseErrorsOne, [name]: ''})
  } else {
    value === ''
    ? setHouseErrorsTwo({...houseErrorsTwo, [name]: emptyFieldErrorMessage})
    :  e.target.value <= 0 
      ? setHouseErrorsTwo({...houseErrorsTwo, [name]: negativeErrorMessage})
      : setHouseErrorsTwo({...houseErrorsTwo, [name]: ''})
  }
 
}

///////////////////////////////////////////////////////////////////////////////////////

function handleChangeInputDrop(e) {
  const {name, value} = e.target;
  setHouse({
    ...house,
    [name]: value
  })
  if (name === 'city') {
      dispatch(getCity(value))
  }
 
 if (value === '') {
setHouseErrorsTwo({...houseErrorsTwo, [name]: emptyFieldErrorMessage})
 }

 if (name === 'city' && value !== '') {
  if (Object.values(dropOptions).every(x => x !== value)) {
    setHouseErrorsTwo({...houseErrorsTwo, 'city': choiceErrorMessage}) 
  } 
} 
}

// useEffect(() => {
//   console.log(house.city, cities)
// }, [house.city,cities])


// изменение значения инпута ГОРОД по дропдауну 
function handleChangeCityDrop(region, city) {

    setHouse({...house, 'city': `${region}, ${city}`, 'region': region, 'name': city })
    setHouseErrorsTwo({...houseErrorsTwo, 'city': ''})

}

///////////////////////////////////////////////////////////////////////////////////////

// КАЛЕНДАРЬ
// подключаем локализацию для календаря
registerLocale('ru', ru)
// изменение и валидация специфического поля календаря

useEffect(() => {
    if (purchaseDate === null) {
      setHouseErrorsOne({...houseErrorsOne, 'calendar': calendarErrorMessage})
      setIsValidOne(false)
    } else if (purchaseDate > worldDate) {
      setHouseErrorsOne({...houseErrorsOne, 'calendar': guestsFromTheFuture})
    } else {
        setHouseErrorsOne({...houseErrorsOne, 'calendar': ''})
        setIsValidOne(true)
      }
},[purchaseDate])

useEffect(() => {
setIsValidOne(false)
}, [realtyCategory])
///////////////////////////////////////////////////////////////////////////////////////

// SUBMIT
  function handleHouseSubmit(e) {
    e.preventDefault();
    console.log(house)
      dispatch(createVillas(house))
    handleNextPage()
  }


///////////////////////////////////////////////////////////////////////////////////////

// ДРОПДАУНЫ
// переменные
const [isOptionCityDown, setIsOptionCityDown] = useState(false);
const [isTypeOfHouseDown, setIsTypeOfHouseDown] = useState(false);

function handleChangeTypeOfHouseDrop(name ,value) {
  setIsTypeOfHouseDown(!isTypeOfHouseDown)
  setHouse({...house, 'typeOfHouse': {name: name, value: value}})
  setHouseErrorsTwo({...houseErrorsTwo, 'typeOfHouse': ''})
}
///////////////////////////////////////////////////////////////////////////////////////


  return (
   <form name="realty-house" className="form popup__form" onSubmit={handleHouseSubmit} autoComplete="off">
            {page===1 
            ? ( <>
              <fieldset className="popup-fildset inputs-fildset">
                <label className="active-form__label date-label"> Дата покупки * 

                  <DatePicker
                    selected={purchaseDate} 
                    onChange={setPurchaseDate}
                    name='date'
                    locale='ru'
                    dateFormat={'dd.MM.yyyy'}
                    calendarStartDay={1}

                    showYearDropdown
                    showMonthDropdown
                  />
                  <span className = "form__item-error ">{houseErrorsOne.calendar}</span>  
                </label>

                  <div className='input-dropdown-box'>
                    <label className="active-form__label">Тип дома *
                        <input name="typeOfHouse" type="text" className="form__input" placeholder="Placeholder" value={house.typeOfHouse.name} onClick={(e) => {setIsTypeOfHouseDown(!isTypeOfHouseDown)}}></input>
                        <span className = "form__item-error ">{houseErrorsTwo.typeOfHouse}</span>
                    </label>
                      <div className={!isTypeOfHouseDown ? 'dropdown': 'dropdown dropdown_open'} >
                        {dropOptionsTypeHouse.map((item) => {

                              return <div className="option" key={item.name} onClick={(e) => {handleChangeTypeOfHouseDrop(item.name,item.value)}}>{item.name}</div>

                        })}
                      </div>
                  </div>

                <div className="form-flex-wraper">
                  <label className="active-form__label flex-label"> Площадь дома, м2 *
                    <input type="number" className="form__input" placeholder="Placeholder" name='squareHome' onChange={handleChangeInput} value={house.squareHome}></input>
                    <span className = "form__item-error ">{houseErrorsOne.squareHome}</span>
                  </label>
                
                  <label className="active-form__label flex-label"> Площадь участка, м2 *
                    <input type="number" className="form__input" placeholder="Placeholder" name='squareLand' onChange={handleChangeInput} value={house.squareLand} ></input>
                    <span className = "form__item-error ">{houseErrorsOne.squareLand}</span> 
                  </label>  
                </div>
        
                <label className="active-form__label"> Стоимость, руб *
                  <input type="number" className="form__input" placeholder="Placeholder" name='cost' onChange={handleChangeInput} value={house.cost} ></input>
                  <span className = "form__item-error ">{houseErrorsOne.cost}</span>
                </label>

              </fieldset>

            
              <fieldset className="popup-fildset">
                <div className="form-flex-wraper">
                  <button type="button" className="white-button button  button-s"  onClick={handleBackPage}>{"Назад"}</button>
                  <button type="button" className="orange-button button  button-s" disabled={!isValidOne} onClick={handleNextPage}>{"Продолжить"}</button>
                </div>
              
                <div className="page-wrapper">
                  <div className= "page-point  page-point_active"></div>
                  <div className="page-point"></div>
                  <div className="page-point"></div>
                </div>
              </fieldset>
              </>)
  // ВТОРАЯ ФОРМА      
              : (page===2) 
              ? (<>
                <fieldset className="popup-fildset">

                    <div className='input-dropdown-box'>
                        <label className="active-form__label">Город *
                            <input name="city" className="form__input" placeholder="Москва" value={house.city} onChange={(e) => {handleChangeInputDrop(e); setIsOptionCityDown(true)}}></input>
                            <span className = "form__item-error ">{houseErrorsTwo.city}</span>
                        </label>
                          <div className={!isOptionCityDown ? 'dropdown': 'dropdown dropdown_open'} >
                            {cities.map((item) => {
                              return <div className="option" key={`city_house_${item.id}`} onClick={(e) => {handleChangeCityDrop(item.region,item.city); setIsOptionCityDown(false)}}>{item.region}, {item.city}</div>
                            })}
                          </div>
                    </div>

                  <label className="active-form__label"> Улица *
                    <input type="text" className="form__input" placeholder="пр. Мира" name="street" onChange={handleChangeInput} value={house.street} ></input>
                    <span className = "form__item-error ">{houseErrorsTwo.street}</span>  
                  </label>
                  
                  <label className="active-form__label"> Дом * 
                    <input type="text" className="form__input" placeholder="35" name="house" onChange={handleChangeInput} value={house.house}></input>
                    <span className = "form__item-error ">{houseErrorsTwo.house}</span> 
                  </label>  
                
                  <label className="checkbox" >
                    <input type="checkbox" className="checkbox-input" name="isStatistics" checked={statisticCheckboxChecked} onChange={() => setStatisticCheckboxChecked(!statisticCheckboxChecked)}></input>
                    <span className='checkbox-switch'></span>
                    <span className='checkbox-text'>Отображать в общей статистике</span>
                  </label>
                </fieldset>     

                <fieldset className="popup-fildset">
                  <div className="form-flex-wraper">
                    <button type="button" className="white-button button button-s" onClick={handleBackPage}>{"Назад"}</button>
                    <input type="submit" className="orange-button button button-s" disabled={!isValidTwo} value="Добавить актив"></input>
                  </div>     


                  <div className="page-wrapper">
                    <div className="page-point"></div>
                    <div className="page-point page-point_active"></div>
                    <div className="page-point"></div>
                  </div>  
                </fieldset>
              </>)

              :  
//ПОДТВЕРЖДЕНИЕ
              <>
                <Comfirmation 
                isOk={isOk}
                  handleClose={handleClose}
                  setActiveCategory={setActiveCategory}
                  page={page}
                  setPage={setPage}
                  setCategory={setRealtyCategory}
                />
                <div className="page-wrapper">
                  <div className="page-point"></div>
                  <div className="page-point"></div>
                  <div className="page-point page-point_active"></div>
                </div>
              </>

            }

          </form>
      )
  }

  export default HouseForm;