import {React, useState, useEffect} from 'react';

import { 
  emptyFieldErrorMessage,
  negativeErrorMessage,
} from "../../../utils/constants";

import Comfirmation from "../Confirmation/Confirmation";
import FinamInstruction from '../../PopupInstruction/FinamInstruction';
import TinkoffInstruction from '../../PopupInstruction/TinkoffInstruction';

import './StocksForm.css'


function StocksForm({page, setPage, handleClose, setActiveCategory, stocksCategory, setStocksCategory}) {

  const [isValid, setIsValid] = useState(false)
  const [statisticCheckboxChecked, setStatisticCheckboxChecked] = useState(true) // состояние чекбокса

  const [tinkoff, setTinkoff] = useState({
    apiKey: '',
  })
  const [tinkoffErrors, setTinkoffErrors] = useState({
    apiKey: null,
  })

  const [finam, setFinam] = useState({
    ID: '',
    apiKey: '',
  })
  const [finamErrors, setFinamErrors] = useState({
    ID: null,
    apiKey: null,
  })


// отслеживает выбор категории недвижимости
function handleChoice(e) {
  setStocksCategory(e.target.name)
  handleNextPage()
}

// переход на следующую страничку попап-формы
function handleNextPage() {
  setPage(page + 1) 
}

// возврат на предыдущую старичку попап-формы
function handleBackPage() {
if (page===1) {
  setStocksCategory('')
  setTinkoff('')
  setFinam('')
  setIsValid(false)
}
if (page===0) {
  setActiveCategory('')
}
setPage(page - 1)
}


    // Отзычивая валидность формы 
  useEffect(() => {
    setIsValid(!Object.values(tinkoffErrors).some(x => x !== ''))
  }, [tinkoffErrors])

  useEffect(() => {
    setIsValid(!Object.values(finamErrors).some(x => x !== ''))
  }, [finamErrors])


//////////////////////////////////////////////////////////////////////////////////////////
// обычный инпут
  function handleChangeInput(e) {
    if (stocksCategory === 'tinkoff') {
      const {name, value} = e.target;
    setTinkoff({
      ...tinkoff,
      [name]: value
    })

      value === ''
      ?setTinkoffErrors({...tinkoffErrors, [name]: emptyFieldErrorMessage})
      :  e.target.value <= 0 
        ? setTinkoffErrors({...tinkoffErrors, [name]: negativeErrorMessage})
        : setTinkoffErrors({...tinkoffErrors, [name]: ''})
  } else {
    const {name, value} = e.target;
    setFinam({
      ...finam,
      [name]: value
    })

      value === ''
      ?setFinamErrors({...finamErrors, [name]: emptyFieldErrorMessage})
      :  e.target.value <= 0 
        ? setFinamErrors({...finamErrors, [name]: negativeErrorMessage})
        : setFinamErrors({...finamErrors, [name]: ''})
  }
    }
    
  useEffect(() => {
    if (stocksCategory === 'tinkoff') {
      setTinkoff({...tinkoff,
        isStatistic: statisticCheckboxChecked,
      })
    } else {
      setFinam({...finam,
        isStatistic: statisticCheckboxChecked,
      })
    }
  }, [statisticCheckboxChecked])


  // функция отправки формы (вызывается на второй странице)
  function handleSubmit(e) {
    e.preventDefault();
    setPage(page + 1)
    if (stocksCategory === 'tinkoff') {
      console.log(tinkoff)
    } else {
      console.log(finam)
    }
    
}

    return (
        <div className={ stocksCategory !== '' ? `popup__container_stocks` : ''}>
          <div className='popup__content'>
            <div className="popup__header">
                <button className={page===2 ? "popup__go-back-button popup__go-back-button_none" : "popup__go-back-button"} onClick={handleBackPage}></button>
                <h3 className="title popup__title">
                  <span className={stocksCategory==="tinkoff"? "card-icon tinkoff" : stocksCategory==="finam" ? "card-icon finam" : "card-icon stocks"}></span>
                  {stocksCategory==="tinkoff" 
                    ? "Брокерский счет/Тинькофф"
                    : stocksCategory==="finam" 
                    ? "Брокерский счет/Финам" 
                    : "Брокерский счет"
                  }
                </h3>
                <p className="paragraph popup__paragraph">
                    Заполнение всех данных поможет нам точнее рассчитать все показатели
                </p>
                <button className="close-button" onClick={handleClose}></button>
            </div>

            { page===0
              ? (
              <div className="category">
                <button className="button category-button" name="tinkoff" onClick={handleChoice}><span className="card-icon tinkoff"></span>Тинькофф<span className="category__button-icon"></span></button>
                <button className="button category-button" name="finam" disabled onClick={handleChoice}><span className="card-icon finam"></span>Финам<span className="category__button-icon"></span></button>
              </div>
              )

              : (stocksCategory === 'tinkoff')
              ? ( page ===1
                ?
                    <form name="stocks" onSubmit={handleSubmit} className="form popup__form">
                        <fieldset className="popup-fildset">
                            <div>
                                <label className="active-form__label">Вы можете дать название активу
                                  <input name="metal" className="form__input" placeholder="Например, на отпуск, на обучение..."></input>
                                  <span className = "form__item-error ">{}</span>
                                </label>  

                                <label className="active-form__label"> API key *
                                    <input type="text" className="form__input" name="apiKey" placeholder="***********" value={tinkoff.apiKey} onChange={handleChangeInput}></input>
                                    <span className = "form__item-error item-error_info">Публичный ключ из личного кабинета Freedom Finance</span>
                                    <span className = "form__item-error ">{tinkoffErrors.apiKey}</span>
                                </label>

                                <label className="checkbox" >
                                    <input type="checkbox" className="checkbox-input" checked={statisticCheckboxChecked} onChange={(e) => setStatisticCheckboxChecked(!statisticCheckboxChecked)}></input>
                                    <span className='checkbox-switch'></span>
                                    <span className='checkbox-text'>Отображать в общей статистике</span>
                                </label>
                            </div>
                        </fieldset>


                        <fieldset className="popup-fildset">
                            <div className="form-flex-wraper">
                                <button type="button" className="white-button button button-s" onClick={handleBackPage}>{"Назад"}</button>
                                <input type="submit" className="orange-button button button-s" disabled={!isValid} value="Добавить актив"></input>
                            </div>

                            <div className="page-wrapper">
                                <div className= "page-point  page-point_active"></div>
                                <div className="page-point"></div>
                            </div>
                        </fieldset>
                    </form>
                  :
                  <>
                      <Comfirmation
                       isOk={true}
                      setCategory={setStocksCategory}
                          handleClose={handleClose}
                          setActiveCategory={setActiveCategory}
                          page={page}
                          setPage={setPage}
                      />

                      <div className="page-wrapper page-wrapper_mini">
                          <div className="page-point"></div>
                          <div className="page-point page-point_active"></div>
                      </div> 
                  </>
                )

                : ( page===1
                  ?
                  <form name="stocks" onSubmit={handleSubmit} className="form popup__form">
                  <fieldset className="popup-fildset">
                      <label className="active-form__label">Вы можете дать название активу
                        <input name="metal" className="form__input" placeholder="Например, на отпуск, на обучение..."></input>
                        <span className = "form__item-error ">{}</span>
                      </label>  

                      <label className="active-form__label"> ID *
                        <input type="text" className="form__input" name="ID" placeholder="***********" value={finam.ID} onChange={handleChangeInput}></input>
                        <span className = "form__item-error ">{finamErrors.ID}</span>
                      </label>
                     
                      <div>
                          <label className="active-form__label"> API key *
                              <input type="text" className="form__input" name="apiKey" placeholder="***********" value={finam.apiKey} onChange={handleChangeInput}></input>
                              <span className = "form__item-error item-error_info">Публичный ключ из личного кабинета Freedom Finance</span>
                              <span className = "form__item-error ">{finamErrors.apiKey}</span>
                          </label>

                          <label className="checkbox" >
                              <input type="checkbox" className="checkbox-input" checked={statisticCheckboxChecked} onChange={(e) => setStatisticCheckboxChecked(!statisticCheckboxChecked)}></input>
                              <span className='checkbox-switch'></span>
                              <span className='checkbox-text'>Отображать в общей статистике</span>
                          </label>
                      </div>
                  </fieldset>

                  <fieldset className="popup-fildset">
                      <div className="form-flex-wraper">
                          <button type="button" className="white-button button button-s" onClick={handleBackPage}>{"Назад"}</button>
                          <input type="submit" className="orange-button button button-s" disabled={!isValid} value="Добавить актив"></input>
                      </div>

                      <div className="page-wrapper">
                          <div className= "page-point  page-point_active"></div>
                          <div className="page-point"></div>
                      </div>
                  </fieldset>
              </form>

                  :
                    <>
                        <Comfirmation
                          isOk={true}
                           setCategory = {setStocksCategory}
                           
                            handleClose={handleClose}
                            setActiveCategory={setActiveCategory}
                            page={page}
                            setPage={setPage}
                        />

                        <div className="page-wrapper page-wrapper_mini">
                            <div className="page-point"></div>
                            <div className="page-point page-point_active"></div>
                        </div> 
                    </>
                )
            }
          </div>
         {stocksCategory==='finam'
         ? <FinamInstruction type={stocksCategory} />
        : <TinkoffInstruction type={stocksCategory} />
        }
        </div>
    )
}

export default StocksForm;