import React from "react";

function TinkoffInstruction({type}) {
return (
  type ==='tinkoff'
  ? (
  <div className='stock__info'>
    <p className='stock__info__title'> Инструкция по подключению:</p>
    <ol className='popup__instruction'>
      <li className='stock__info__paragraph'>Перейдите в настройках профиля Тинькофф Инвестиции по <a href="#" className="link">ссылке</a>.</li>
      <li className='stock__info__paragraph'>Авторизуйтесь в системе, если это требуется.</li>
      <li className='stock__info__paragraph'>Функция «Подтверждение сделок кодом» должна быть отключена.</li>
      <li className='stock__info__paragraph'>Выпустите токен TINKOFF INVEST API для биржи (при создании, выберите Readonly token). Возможно, система попросит вас авторизоваться ещё раз. Не беспокойтесь, это необходимо для подключения робота к торговой платформе.</li>
      <li className='stock__info__paragraph'>Скопируйте токен и сохраните его. Токен отображается только один раз, просмотреть его позже не получится. Тем не менее вы можете выпускать неограниченное количество токенов.</li>
    </ol>
    <p className='paragraph'>Более подробную инструкцию по подключению вы можете посмотреть <a href="#" className="link">здесь</a></p>
   </div>
)
: <></>
)
}

export default TinkoffInstruction;