import React from "react";

function FinamInstruction({type}) {
  return (
    type ==='finam'
    ? (
      <div className='stock__info'>
        <p className='stock__info__title'>Инструкция по подключению:</p>
        <ol className='popup__instruction'>
          <li className='stock__info__paragraph'>Открыть счёт в <a href="#" className="link">«Финаме»</a>.</li>
          <li className='stock__info__paragraph'>Зарегистрироваться в сервисе <a href="#" className="link">Comon</a>.</li>
          <li className='stock__info__paragraph'>В личном кабинете Comon получить <a href="#" className="link">токен</a>.</li>
        </ol>

        <p className='stock__info__title'>Получение нового токена:</p>
        <ol className='popup__instruction'>
          <li className='stock__info__paragraph'>Нажать кнопку + создать токен.</li>
          <li className='stock__info__paragraph'>Выбрать счёт. Можно выбрать как один, так и несколько счётов.</li>
          <li className='stock__info__paragraph'>Указать название, выбрать тип «Просмотровый», период срока действия (для вашего удобства, рекомендуем выбрать бессрочный период действия токена) и нажать + создать токен.</li>
          <li className='stock__info__paragraph'>Подтвердить действие.</li>
          <li className='stock__info__paragraph'>Скопировать и сохранить полученный токен.</li>
        </ol>
        <p className='stock__info__paragraph'>Более подробную инструкцию по подключению вы можете посмотреть <a href="#" className="link">здесь</a></p>
      </div>
    )
    : <></>
  )
}

export default FinamInstruction;