import React, {useState} from "react";

import "./Main.css"

import Header from "../../components/Header/Header";
import Sidebar from "../../components/Sidebar/Sidebar";
import CapittalCard from "../../components/CapitalCard/CapitalCard";
import MainSots from "../../components/MainSots/MainSots";

import Popup from "../../components/Popup/Popup";
import Notification from "../../components/NotificationPopup/NotificationPopup";

import { useEffect } from "react";

import {useDispatch, useSelector} from "react-redux";
import {fetchData} from "../../store/reducers/RocetSlice";
import {getBrand, getCity} from "../../store/reducers/Actives/Drops";



function Main({ logOut, handleOpenNotification, openNotification, switchTheme, handlerOpenPopup, closePopups, newActivePopupOpened, isOk, setIsOk, user}) {


    const rocketData = useSelector((state) => state.rocketSlice.data);
    const dispatch = useDispatch()
    const loadData = useSelector((state) => state.rocketSlice.loading);




  
    //////////////////////////////////////////////////////////////////////////
    // временная мера для модалок с редактированием //

  ///////////////////////////////////////////////////////////////////////////////////////////////
//  digital_rubles.objects.statistic[0].current_price  digital_rubles.profit_percentage

    useEffect(() => {
        dispatch(fetchData())
        dispatch(getBrand())
        dispatch(getCity())
    }, []);

  //   const users = [
  //   { id: 1, cost:  sota1 , percent: 1 },
  //   { id: 2, cost: 88888 , percent: 0 },
  //   { id: 3, cost: 8888888, percent: 0 },
  //   { id: 4, cost: 88888888, percent: 5 },
  //   { id: 5, cost: 888888888, percent: 5 },
  //   { id: 6, cost: 9999999999, percent: 5 },
  //   { id: 7, cost: 999999999, percent: 5 },
  //   { id: 8, cost: 9999, percent: 5 },
  //   { id: 9, cost: 7777777, percent: 5 },
  //   { id: 10, cost: 0  , percent: 5 },
  //   { id: 11, cost: 77, percent: 5 },
  //   { id: 12, cost: 0 , percent: 0 },
  //   { id: 13, cost: 0 , percent: 0},
  //   { id: 14, cost: 531001, percent: 5 },
  //   { id: 15, cost: 531001, percent: 5 },
  //   { id: 16, cost: 531001, percent: 5 },
  //   { id: 17, cost: 531001, percent: 5 },
  //   { id: 18, cost: 300000, percent: 5 },
  //   { id: 19, cost: 300000, percent: 5 },
  //   { id: 20, cost: 300000, percent: 5 },
  //   { id: 21, cost: 300000, percent: 5 },
  // ]

  return (
    <>
        <div className="main">
      <Header 
        openNotification={handleOpenNotification}
        switchTheme={switchTheme}
        />
      <Sidebar logOut ={logOut}/>

            {loadData === true && rocketData !== null ?
                (rocketData.total_quantity !== 0
                        ? (<MainSots
                            // users={users}
                            handlerOpenPopup={handlerOpenPopup}
                        />)
                        : (<CapittalCard
                       

                            handlerOpenPopup={handlerOpenPopup}
                        />)
                )
                 : (null)} 

      
        </div>

        <Popup

        user={user}
        isOk={isOk}
        setIsOk={setIsOk}

        isOpened={newActivePopupOpened}
        isClose={closePopups}

        />
        <Notification
        isOpen={openNotification}
        onClose={closePopups}
        />

    </>
  )
}

export default Main;