import React from "react";

function BinansInstruction({type}) {
  return (
    type ==='market'
    ? (
      <div className='stock__info'>
    <p className='stock__info__title'>Как создать API ключ:</p>
    <ol className='popup__instruction'>
      <li className='stock__info__paragraph'>Перейдите по <a href="#" className="link">ссылке</a></li>
      <li className='stock__info__paragraph'>Нажмите на «Создать API», выберите «Сгенерировать системой» и нажмите «Далее».</li>
      <li className='stock__info__paragraph'>Введите название для ключа, например «ТММ», и нажмите «Далее».</li>
      <li className='stock__info__paragraph'>Скопируйте ключ API и секретный ключ, затем вставьте их в форму слева. Ключи создаются «только для чтения» по умолчанию, вам не нужно ничего менять</li>
    </ol>
    <p className='stock__info__paragraph'>В <a href="#" className="link">настройках</a>, мы рекомендуем указать наш IP 31.129.102.22 для вашей безопасности и бессрочного действия токена.</p>
  
    </div>
    )
    : <></>
  )
}

export default BinansInstruction;