import "./RealtyEdit.css"

import {React, useEffect, useState} from "react";
import DatePicker, {registerLocale} from "react-datepicker";

import ru from "date-fns/locale/ru";

import {
    calendarErrorMessage,
    emptyFieldErrorMessage,
    negativeErrorMessage,
    dropOptionsTypeLandPlot,
} from "../../../../utils/constants";
import {useDispatch, useSelector} from "react-redux";
import {updatePlots} from "../../../../store/reducers/RocetSlice";


function LandPlotEdit({
       page,
       setPage,
       handleClose,
       isOpened,
       realtyCategory
   }) {

    

    const dispatch = useDispatch();

    const realtyItem =  useSelector((state) => state.rocketSlice.plots.objects[state.rocketSlice.activeId]);
    const [isDropDown, setIsDropDown] = useState(false);
    const [isDropDownType, setIsDropDownType] = useState(false);
    const [isChecked, setIsChecked] = useState(true)
    const [edit, setEdit] = useState(false)
    const [openModal, setOpenModal] = useState(false)

    const fullAddress = `${realtyItem.address.city}, ${realtyItem.address.street}, ${realtyItem.address.house_number}`

    const [isRoomClassDown, setIsRoomClassDown] = useState(false)
    const [isPurposeDown, setIsPurposeDown] = useState(false);
    const [startDate, setStartDate] = useState(new Date());
    registerLocale('ru', ru)

    function getModel () {
        setOpenModal(!openModal)
    }
    function handleChangeCheckbox() {
        setIsChecked(!isChecked)
    }

    function handleSubmit(e) {
        e.preventDefault();
        setPage(page + 1)
    }

    function updateEdit () {
        setEdit(!edit)
    }
/////////////////////////////////////////////////////////////////////////
    const [isValid, setIsValid] = useState(false)

    const [communications, setСommunications] = useState('');
    const [realty, setRealty] = useState({
        type: {name: '', value: ''},

        square: '',
        startDate: '',
        startCost: '',

        endPrice: 12000000,
        income: 5,
        profit: 1000000,
    });

    const [realtyErrors, setRealtyErrors] = useState({
        square: null,
        startDate: '',
        startCost: null,
    });

    function MyComponent() {
        async function fetchData() {
            try {
                await console.log()
                const typeHouse = dropOptionsTypeLandPlot.filter(type => type.value === realtyItem.land_category)
                setRealty({
                    type:{name: typeHouse[0].name, value: typeHouse[0].value},

                    square: realtyItem.total_area,
                    startDate: realtyItem.start_date,
                    startCost: realtyItem.price,

                    endPrice: realtyItem.statistic[0] === undefined ? realtyItem.current_price :
                        realtyItem.statistic[0].current_price,
                    income: realtyItem.statistic[0] === undefined ? 0 :
                        realtyItem.statistic[0].profit_percentage,
                    profit: realtyItem.statistic[0] === undefined ? 0 :
                        realtyItem.statistic[0].profit_amount,

                    isStatistic: realtyItem.display_in_general
                });
                setRealtyErrors({
                    square: '',
                    startDate: '',
                    startCost: '',
                });
                setStartDate(new Date(realtyItem.start_date))
            } catch (error) {
                console.log(error)
            }
        }

        fetchData();
    }
    useEffect(() => {
        MyComponent();
    }, [realtyItem])
////////////////////////////////////////////////////////////////////////////////////////
    // изменение значения стандартных инпутов
    function handleChangeInput(e) {
        const {name, value} = e.target;
        if (realtyCategory === 'plots'){
            setRealty({
                ...realty,
                [name]: value
            })
            value === ''
                ? setRealtyErrors({...realtyErrors, [name]: emptyFieldErrorMessage})
                :  e.target.value <= 0
                    ? setRealtyErrors({...realtyErrors, [name]: negativeErrorMessage})
                    : setRealtyErrors({...realtyErrors, [name]: ''})
        }
    }
    function handleSubmit(e) {
        e.preventDefault();
        dispatch(updatePlots(realtyItem.id,realty))
        console.log(realty)
    }
// Отзычивая валидность формы жилой недвижимости
    useEffect(() => {
        if (realtyCategory === 'plots'){
            setIsValid(!Object.values(realtyErrors).some(x => x !== ''))
        }
    })

    useEffect(() => {
        if (realtyCategory === 'plots'){
            setRealty({...realty,
                isStatistic: isChecked,
                startDate: startDate,
                communications: communications,
            });
        }
    }, [isChecked, startDate, communications])


    // изменение и валидация специфического поля календаря
    useEffect(() => {
        if (realtyCategory === 'plots'){
            if (realty.startDate === null) {
                setRealtyErrors({...realtyErrors, 'startDate': calendarErrorMessage})
                setIsValid(false)
            } else {
                setRealtyErrors({...realtyErrors, 'startDate': ''})
                setIsValid(true)
            }
        }
    },[realty.startDate])

    const [isTypeOfHouseDown, setIsTypeOfHouseDown] = useState(false);

    function handleChangeTypeOfHouseDrop(name,value) {
        setIsTypeOfHouseDown(!isTypeOfHouseDown)
        setRealty({...realty, 'type': {name: name, value: value}})
        setRealtyErrors({...realtyErrors, 'type': ''})
    }

    return (
        <div className="edit edit-realty">
            <div className={isOpened ? "popup popup_opened" : "popup" }>
                <div className="popup__container__transport-edit popup__container-edit">
                    <div className="popup__header">
                        <button className="popup__go-back-button"></button>
                        <h3 className="title popup__title">
                            <span className={"card-icon landPlot"}></span>
                            Участок
                        </h3>
                        <button className="close-button" onClick={handleClose}></button>
                    </div>

                    <div className="edit-chart">
                        <div className="edit-chart-box realty__edit__chart">
                            <p className="paragraph_l all-no-margin">{realty.endPrice} ₽</p>
                            <p className="paragraph_m text-green all-no-margin">{`↑ ${realty.profit === null ? 0 : realty.profit} (${realty.income === null ? 0 : realty.income }%)`}</p>
                        </div>
                    </div>

                    <div className="edit-form">
                        <div className="edit-on" onClick={getModel}>
                            <div className={openModal === false ? "edit-on-button" : "edit-on-button display-block"} onClick={updateEdit}>
                                <p className="paragraph button-edit button-top">{edit === true ? "Отмена" : "Редактировать"}</p>
                                <p className="paragraph button-edit button-bottom">Удалить</p>
                            </div>
                        </div>
                        <details className="drop-edit" open>
                            <summary className="paragraph drop-edit-text">{fullAddress}
                            </summary>
                            <form name="realty-residential" className={edit === true ? "form popup__form__deposit__active-edit bg-grey" :"form popup__form__deposit__disabled-edit bg-grey-full" } onSubmit={handleSubmit}>
                                <fieldset className="popup-fildset inputs-fildset">

                                    <div className='input-dropdown-box'>
                                        <label className="active-form__label"> Назначение
                                            <input type="text" name='type' disabled={!edit} className={edit === false ? "form__input input-disabled" : "form__input bg-white-light"} placeholder="Населенный пункт" onClick={(e) => {setIsTypeOfHouseDown(!isTypeOfHouseDown)}} value={realty.type.name}></input>
                                            <span className = "form__item-error ">{realtyErrors.type}</span>
                                        </label>
                                        <div className={!isTypeOfHouseDown ? 'dropdown': 'dropdown dropdown_open'} >
                                            {dropOptionsTypeLandPlot.map((item) => {

                                                return <div className="option" key={item.name} onClick={(e) => {handleChangeTypeOfHouseDrop(item.name,item.value)}}>{item.name}</div>

                                            })}
                                        </div>
                                    </div>

                                        <label className="active-form__label"> Общая площадь, м2 *
                                            <input type="number" className={edit === false ? "form__input input-disabled" : "form__input bg-white-light"} placeholder="60" name='square'  disabled={!edit} onChange={handleChangeInput} value={realty.square}></input>
                                            <span className = "form__item-error bg-gray-edit">{realtyErrors.square}</span>
                                        </label>
                                    <label className="active-form__label date-label"> Дата покупки *

                                        <DatePicker
                                            selected={startDate}
                                            onChange={setStartDate}

                                            locale='ru'
                                            dateFormat={'dd.MM.yyyy'}
                                            calendarStartDay={1}

                                            showYearDropdown
                                            showMonthDropdown
                                            inputType='number'
                                            disabled={!edit}
                                        />
                                        <span className = "form__item-error bg-grey-edit">{realtyErrors.startDate}</span>
                                    </label>
                                    <div className="form-flex-wraper">
                                        <label className="active-form__label flex-label"> Начальная цена
                                            <input type="text" className={edit === false ? "form__input input-disabled" : "form__input bg-white"} placeholder="10 000 000 ₽" name='startCost' value={realty.startCost} disabled></input>
                                            <span className = "form__item-error bg-grey-edit">{realtyErrors.startCost}</span>
                                        </label>

                                        <label className="active-form__label flex-label"> Текущая цена
                                            <input type="text" className={edit === false ? "form__input input-disabled" : "form__input bg-white"} placeholder="11 000 000 ₽" name='nowCost' value={realty.endPrice + ' ' + '₽'} disabled></input>
                                        </label>
                                    </div>

                                    <div className="form-flex-wraper">
                                        <label className="active-form__label flex-label"> Доходность
                                            <input type="text" className={edit === false ? "form__input input-disabled" : "form__input bg-white"} placeholder="+ 5%" name='yield' value={'+' + ' ' + realty.income + '%'} disabled></input>
                                        </label>

                                        <label className="active-form__label flex-label"> Прибыль
                                            <input type="text" className={edit === false ? "form__input input-disabled" : "form__input bg-white"} placeholder="1 000 000 ₽" name='profit' value={realty.profit + ' ' + '₽'} disabled></input>
                                        </label>
                                    </div>
                                </fieldset>

                                <div className="button__edit-box">
                                    <label className="checkbox center checkbox-edit" >
                                        <input type="checkbox" className="checkbox-input" checked={realty.isStatistic} onChange={handleChangeCheckbox}></input>
                                        <span className='checkbox-switch'></span>
                                        <span className='checkbox-text'>Отображать в общей статистике</span>
                                    </label>
                                    <button className={edit === true ? "button orange-button button-no-margin button-custom" : "orange-button button-no-margin button-disabled"} onClick={updateEdit} disabled={!isValid}>Сохранить изменения</button>
                                </div>
                            </form>
                        </details>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default LandPlotEdit