import {React, useState, useEffect} from "react";

import DatePicker, {registerLocale} from "react-datepicker";
import ru from 'date-fns/locale/ru';

import Comfirmation from "../Confirmation/Confirmation";

import {
  dropOptions,
  guestsFromTheFuture,
  calendarErrorMessage,
  emptyFieldErrorMessage,
  negativeErrorMessage,
  choiceErrorMessage, dropOptionsTypeLandPlot,
} from "../../../utils/constants";

import './RealtyForm.css';
import {useDispatch, useSelector} from "react-redux";
import {getCity} from "../../../store/reducers/Actives/Drops";
import {createPlots} from "../../../store/reducers/RocetSlice";

function LandPlotForm({
  isOk,
  handleBackPage,
  handleNextPage,
  page, 
  setPage, 
  handleClose,
  realtyCategory, 
  setRealtyCategory, 
  setActiveCategory
  }) {

  const dispatch = useDispatch();
  const cities = useSelector((state) => state.drops.city);
  const [isValidOne, setIsValidOne] = useState(false)
  const [isValidTwo, setIsValidTwo] = useState(false)
  const [isPurposeDown, setIsPurposeDown] = useState(false)
  const [isOptionCityDown, setIsOptionCityDown] = useState(false);

  const [purchaseDate, setPurchaseDate] =useState(new Date());
  const [statisticCheckboxChecked, setStatisticCheckboxChecked] = useState(true) // состояние чекбокса

  const worldYear = new Date;

///////////////////////////////////////////////////////////////////////////////////////
const [landPlot, setLandPlot] = useState({

  square:'',
  cost:'',
  landClass: {name: '', value: ''},

  city: '',
  street:'',
  house: '',

  name: '',
  region: '',

})
const [landPlotErrorsOne, setLandPlotErrorsOne] = useState({
  calendar: '',
  square: null,
  cost: null,
  landClass: null,
})

const [landPlotErrorsTwo, setLandPlotErrorsTwo] = useState({
  city: null,
  street: null,
  house: null,
})
///////////////////////////////////////////////////////////////////////////////////////

// добавляет в объект полей значения специфических инпутов при их изменении
useEffect(() => {
  setLandPlot({... landPlot,
    date: purchaseDate,
    isStatistic: statisticCheckboxChecked,
  })

  },[
      purchaseDate,
      statisticCheckboxChecked,
    ])


// Отзычивая валидность формы жилой недвижимости
useEffect(() => {
  page === 1
    ? setIsValidOne(!Object.values(landPlotErrorsOne).some(x => x !== ''))
    :  setIsValidTwo(!Object.values(landPlotErrorsTwo).some(x => x !== '' ))

}, [landPlotErrorsOne,
    landPlotErrorsTwo])
///////////////////////////////////////////////////////////////////////////////////////

// изменение значений стандартных инпутов на первой страничке формы жилой недвижимости
function handleChangeLandPlotInput(e) {
  const {name, value} = e.target;
  setLandPlot({
    ...landPlot,
    [name]: value
  })
  if (page === 1){
    value === ''
        ? setLandPlotErrorsOne({...landPlotErrorsOne, [name]: emptyFieldErrorMessage})
        :  e.target.value <= 0
            ? setLandPlotErrorsOne({...landPlotErrorsOne, [name]: negativeErrorMessage})
            : setLandPlotErrorsOne({...landPlotErrorsOne, [name]: ''}) 
  } else {
    value === ''
        ? setLandPlotErrorsTwo({...landPlotErrorsTwo, [name]: emptyFieldErrorMessage})
        :  e.target.value <= 0
            ? setLandPlotErrorsTwo({...landPlotErrorsTwo, [name]: negativeErrorMessage})
            : setLandPlotErrorsTwo({...landPlotErrorsTwo, [name]: ''})
  }
}

///////////////////////////////////////////////////////////////////////////////////////

function handleChangeLandPlotInputDrop(e) {
  const {name, value} = e.target;
  setLandPlot({
    ...landPlot,
    [name]: value
  })
  dispatch(getCity(value))

 if (value === '') {
setLandPlotErrorsTwo({...landPlotErrorsTwo, [name]: emptyFieldErrorMessage})
 }
 if (name === 'landClass' && value !== '') {
  if (Object.values(dropOptions).every(x => x !== value)) {
    setLandPlotErrorsOne({...landPlotErrorsOne, 'landClass': choiceErrorMessage}) 
  } 
} 
if (name === 'city' && value !== '') {
  if (Object.values(dropOptions).every(x => x !== value)) {
    setLandPlotErrorsTwo({...landPlotErrorsTwo, 'city': choiceErrorMessage}) 
  } 
}
}

// изменение значения инпута ГОРОД по дропдауну 
// поле встречается в ОБЕИХ формах
function handleChangeCityDrop(region, city) {
  setLandPlot({...landPlot, 'city': `${region}, ${city}`, 'region': region, 'name': city })
  setLandPlotErrorsTwo({...landPlotErrorsTwo, 'city': ''})
}
// изменение значения инпута КЛАСС ПОМЕЩЕНИЯ по дропдауну
function handleChangePurposeDrop(name,value) {
  setIsPurposeDown(false)
  setLandPlot({...landPlot, 'landClass': {name: name, value: value}})
  setLandPlotErrorsTwo({...landPlotErrorsTwo, 'landClass': ''})
}
///////////////////////////////////////////////////////////////////////////////////////


// КАЛЕНДАРЬ
// подключаем локализацию для календаря
registerLocale('ru', ru)
// изменение и валидация специфического поля календаря
useEffect(() => {
  if (purchaseDate === null) {
    setLandPlotErrorsOne({...landPlotErrorsOne, 'calendar': calendarErrorMessage})
    setIsValidOne(false)
  } else if (purchaseDate > worldYear) {
    setLandPlotErrorsOne({...landPlotErrorsOne, 'calendar': guestsFromTheFuture})
  } else {
    setLandPlotErrorsOne({...landPlotErrorsOne, 'calendar': ''})
    setIsValidOne(true) 
  }
},[purchaseDate])

useEffect(() => {
setIsValidOne(false)
}, [realtyCategory])

///////////////////////////////////////////////////////////////////////////////////////

// SUBMIT
  function handleLandPlotSubmit(e) {
    e.preventDefault();
    console.log(landPlot)
    dispatch(createPlots(landPlot))
    handleNextPage()
  }

return (
  <form name="realty-residential" className="form popup__form" onSubmit={handleLandPlotSubmit}>
            {page===1 
            ? (<>
                <fieldset className="popup-fildset">
                  <label className="active-form__label date-label"> Дата покупки * 
                    <DatePicker
                     selected={purchaseDate} 
                     onChange={setPurchaseDate}
                      name='date'
                      locale='ru'
                      dateFormat={'dd.MM.yyyy'}
                      calendarStartDay={1}

                      showYearDropdown
                      showMonthDropdown
                    />
                      <span className = "form__item-error ">{landPlotErrorsOne.calendar}</span>  
                  </label>

         
                    <label className="active-form__label "> Общая площадь, м2 *
                      <input type="number" className="form__input" placeholder="Placeholder" name='square' onChange={handleChangeLandPlotInput} value={landPlot.square}></input>
                      <span className = "form__item-error email-input-error">{landPlotErrorsOne.square}</span> 
                    </label>  
                 
    
                  <label className="active-form__label"> Стоимость, руб *
                    <input type="number" className="form__input" placeholder="Placeholder" name='cost' onChange={handleChangeLandPlotInput} value={landPlot.cost}></input>
                    <span className = "form__item-error email-input-error">{landPlotErrorsOne.cost}</span>
                  </label>
                  <div className='input-dropdown-box'>
                    <label className="active-form__label">Категория земли * 
                      <input name="landClass" type="text" className="form__input" placeholder="Placeholder" value={landPlot.landClass.name} onClick={(e) => {setIsPurposeDown(!isPurposeDown)}}></input>
                      <span className = "form__item-error ">{landPlotErrorsTwo.landClass}</span>
                    </label>
                    <div className={!isPurposeDown ? 'dropdown': 'dropdown dropdown_open'} >
                      {dropOptionsTypeLandPlot.map((item) => {
                            return <div className="option" key={item.name} onClick={(e) => {handleChangePurposeDrop(item.name,item.value)}}>{item.name}</div>
                      })}
                    </div>
                  </div>
                </fieldset>

                <fieldset className="popup-fildset">
                  <div className="form-flex-wraper">
                    <button type="button" className="white-button button button-s" onClick={handleBackPage}>{"Назад"}</button>
                    <button type="button" className="orange-button button button-s" disabled={!isValidOne} onClick={handleNextPage}>{"Продолжить"}</button>
                  </div>
                  
                  <div className="page-wrapper">
                    <div className= "page-point  page-point_active"></div>
                    <div className="page-point"></div>
                    <div className="page-point"></div>
                  </div>
                </fieldset>
              </>)

            : (page===2)
            ? (<>
              <fieldset className="popup-fildset">

                <div className='input-dropdown-box'>
                  <label className="active-form__label">Город *
                        <input name="city" type="text" className="form__input" placeholder="Москва" value={landPlot.city} onChange={(e) => {handleChangeLandPlotInputDrop(e); setIsOptionCityDown(true)}}></input>
                        <span className = "form__item-error ">{landPlotErrorsTwo.city}</span>
                  </label>
                  <div className={!isOptionCityDown ? 'dropdown': 'dropdown dropdown_open'} >
                    {cities.map((item) => {
                      return <div className="option" key={`city_residental_${item.id}`} onClick={(e) => {handleChangeCityDrop(item.region ,item.city); setIsOptionCityDown(false)}}>{item.region}, {item.city}</div>
                    })}
                  </div>
                </div>


                <label className="active-form__label"> Улица *
                  <input type="text" className="form__input" placeholder="пр. Мира" name="street" onChange={handleChangeLandPlotInput} value={landPlot.street}></input>
                  <span className = "form__item-error">{landPlotErrorsTwo.street}</span>  
                </label>
            
                <label className="active-form__label"> Дом *
                  <input type="text" className="form__input" placeholder="35" name="house"onChange={handleChangeLandPlotInput} value={landPlot.house}></input>
                  <span className = "form__item-error">{landPlotErrorsTwo.house}</span> 
                </label>  
                
                  <label className="checkbox" >
                    <input type="checkbox" className="checkbox-input" name="isStatistics" checked={statisticCheckboxChecked} onChange={() => setStatisticCheckboxChecked(!statisticCheckboxChecked)}></input>
                    <span className='checkbox-switch'></span>
                    <span className='checkbox-text'>Отображать в общей статистике</span>
                  </label>
              </fieldset>

              <fieldset className="popup-fildset">
                <div className="form-flex-wraper">
                  
                  <button type="button" className="white-button button button-s" onClick={handleBackPage}>Назад</button>
                  <input type="submit" className="orange-button button button-s" disabled={!isValidTwo}  value="Добавить актив"></input>
                </div>     

                <div className="page-wrapper">
                  <div className="page-point"></div>
                  <div className="page-point page-point_active"></div>
                  <div className="page-point"></div>
                </div>
              </fieldset>         
            </>)

      
    //ПОДТВЕРЖДЕНИЕ        
            :
            (<>
              <Comfirmation 
              isOk={isOk}
                handleClose={handleClose}
                setActiveCategory={setActiveCategory}
                page={page}
                setPage={setPage}
                setCategory={setRealtyCategory}
              />
              <div className="page-wrapper">
                <div className="page-point"></div>
                <div className="page-point"></div>
                <div className="page-point page-point_active"></div>
              </div>
            </>)
          }
        </form>
  )
}

export default LandPlotForm;