import "./TransportEdit.css"

import {React, useEffect, useState} from "react";
import DatePicker, {registerLocale} from "react-datepicker";

import ru from "date-fns/locale/ru";

import {
    calendarErrorMessage,
    emptyFieldErrorMessage,
    negativeErrorMessage,
    greatValue,
    retroTransport,
    motorTypeOptions,
    motorSizeOptions,
    transmissionTypeOptions,
} from "../../../../utils/constants";
import {useDispatch, useSelector} from "react-redux";
import {getBrand, getModel} from "../../../../store/reducers/Actives/Drops";
import {updateBike, updateCars} from "../../../../store/reducers/RocetSlice";


function TransportEdit({
        handleClose,
        isOpened,
        transportCategory,
    }) {

    const dispatch = useDispatch();
    const carItem = useSelector((state) => state.rocketSlice.cars.objects[state.rocketSlice.activeId]);
    const bikeItem = useSelector((state) => state.rocketSlice.motorcycles.objects[state.rocketSlice.activeId]);

    const brands = useSelector((state) => state.drops.brand);
    const models = useSelector((state) => state.drops.model);
    const loading = useSelector((state) => state.drops.loading);

    const worldYear = new Date;
    const [isChecked, setIsChecked] = useState(true)
    const [edit, setEdit] = useState(false)
    const [openModal, setOpenModal] = useState(false)

    const [isOptionYearDown, setIsOptionYearDown] = useState(false);
    const [isOptionBrandDown, setIsOptionBrandDown] = useState(false);
    const [isOptionModelDown, setIsOptionModelDown] = useState(false);
    const [isOptionMotorDown, setIsOptionMotorDown] = useState(false);
    const [isOptionMotorSizeDown, setIsOptionMotorSizeDown] = useState(false);
    const [isOptionTransmissionDown, setIsOptionTransmissionDown] = useState(false)



    const [isValid, setIsValid] = useState(false)

    const [startDate, setStartDate] = useState(new Date());
    registerLocale('ru', ru)
    const [transportCar, setTransportCar] = useState({

         brand: {name:``, value: ''},
         model: {name:``, value: ``},
         mileage: '',
         motor: {name: '', value: ''},
         motorSize: '',
         yearTransport: '',
         transmission: {name: '', value: ''},
         date: '',
         startPrice: '',

         endPrice: 12000000,
         income: 5,
         profit: 1000000,
    });
    const [transportCarErrors, setTransportCarErrors] = useState({
        calendar: '',
        brand: null,
        model: null,
        mileage: null,
        motor: null,
        yearTransport: null,
        motorSize: null,
        transmission: null,
        startPrice: null,
    })

////////////////////////////////////////////////////////////////////////////////////////

    const [transportMoto, setTransportMoto] = useState({
        brand: '',
        model: '',
        mileage: '',
        motorSize: '',
        yearTransport: '',
        startPrice: '',
        data: '',

        endPrice: 12000000,
        income: 5,
        profit: 1000000,
    });

    const [transportMotoErrors, setTransportMotoErrors] = useState({
        calendar: '',
        brand: null,
        model: null,
        mileage: null,
        motorSize: null,
        yearTransport: null,
        startPrice: null,
    });

    function MyComponent() {
        async function fetchData() {
            try {
                if (transportCategory === 'bike') {
                    await console.log()
                    const nameTransport = `${bikeItem.brand},${bikeItem.model}`
                    setTransportMoto({
                        brand: bikeItem.brand,
                        model: bikeItem.model,
                        mileage: bikeItem.mileage,
                        motorSize: bikeItem.engine_capacity,
                        yearTransport: bikeItem.production_year,
                        startPrice: bikeItem.price,
                        data: new Date(bikeItem.purchase_date),

                        endPrice:bikeItem.statistic[0] === undefined ? bikeItem.price :
                        bikeItem.statistic[bikeItem.statistic.length - 1].current_price,
                        income: bikeItem.statistic[0] === undefined ? 0 :
                        bikeItem.statistic[bikeItem.statistic.length - 1].profit_percentage,
                        profit: bikeItem.statistic[0] === undefined ? 0 :
                        bikeItem.statistic[bikeItem.statistic.length - 1].profit_amount,

                        isStatistic: bikeItem.display_in_general,

                        transport: nameTransport
                    });
                    setTransportMotoErrors({
                        calendar: '',
                        brand: '',
                        model: '',
                        mileage: '',
                        motorSize: '',
                        yearTransport: '',
                        startPrice: '',
                    });
                    setStartDate(new Date(bikeItem.purchase_date))
                }
                if (transportCategory === 'car'){
                    await dispatch(getModel(brands[carItem.brand - 1].id));
                    const modelsId = models.filter(model => model.id === carItem.model);

                    const transportName = `${brands[carItem.brand - 1].name}, ${modelsId[0].name}`

                    const motorType = motorTypeOptions.filter(motor => motor.value === carItem.engine_type);
                    const transmissionType = transmissionTypeOptions.filter(transmission => transmission.value === carItem.gearbox_type);

                    if (loading === true){
                        setTransportCar({
                            brand: {name:`${brands[carItem.brand - 1].name}`, value: brands[carItem.brand - 1].id},
                            model: modelsId[0]
                                ? { name: `${modelsId[0].name}`, value: `${modelsId[0].id}` }
                                : { name: '', value: '' },
                            mileage: carItem.mileage,
                            motor:motorType[0]
                            ? {name: motorType[0].name, value: motorType[0].value}
                            : {name: "None", value: "None"},
                            motorSize: parseFloat(carItem.engine_capacity).toFixed(1),
                            yearTransport: carItem.production_year,
                            transmission: transmissionType[0]
                                ? {name: transmissionType[0].name, value: transmissionType[0].value}
                                : {name: "None", value: "None"},
                            date: new Date(carItem.purchase_date),
                            startPrice: carItem.price,

                            endPrice: carItem.statistic[0] === undefined ? carItem.price :
                            carItem.statistic[carItem.statistic.length - 1].current_price,
                            income: carItem.statistic[0] === undefined ? 0 :
                            carItem.statistic[carItem.statistic.length - 1].profit_percentage,
                            profit: carItem.statistic[0] === undefined ? 0 :
                            carItem.statistic[carItem.statistic.length - 1].profit_amount,

                            isStatistic: carItem.display_in_general,

                            transport: transportName
                        })
                        setTransportCarErrors({
                            calendar: '',
                            brand: '',
                            model: '',
                            mileage: '',
                            motor: '',
                            yearTransport: '',
                            motorSize: '',
                            transmission: '',
                            startPrice: '',
                        })
                        setStartDate(new Date(carItem.purchase_date))
                    }
                }
            } catch (error) {
                console.log(error)
            }
        }

        fetchData();
    }

    useEffect(() => {
        MyComponent();
    }, [loading, carItem, bikeItem])


// Отзычивая валидность формы редактирования транспорта
    useEffect(() => {
        if (transportCategory === 'car') {
            setIsValid(!Object.values(transportCarErrors).some(x => x !== ''))
        }
        else if (transportCategory === 'bike') {
            setIsValid(!Object.values(transportMotoErrors).some(x => x !== ''))
        }
    }, [transportCarErrors, transportMotoErrors])
////////////////////////////////////////////////////////////////////////////////////////

    // добавляет в объект полей значения специфических инпутов при их изменении
    useEffect(() => {
        if (transportCategory === 'car'){
            setTransportCar({... transportCar,
                date: startDate,
                isStatistic: isChecked,
            })
        } else{
            setTransportMoto({... transportMoto,
                date: startDate,
                isStatistic: isChecked,
            })
        }
    }, [startDate,
        isChecked,
    ])
////////////////////////////////////////////////////////////////////////////////////////
// обычный инпут
    function handleChangeInput(e) {
        const {name, value} = e.target;
        if (transportCategory === 'car'){
            setTransportCar({
                ...transportCar,
                [name]: value
            })

            value === ''
                ? setTransportCarErrors({...transportCarErrors, [name]: emptyFieldErrorMessage})
                :  e.target.value <= 0
                    ? setTransportCarErrors({...transportCarErrors, [name]: negativeErrorMessage})
                    : setTransportCarErrors({...transportCarErrors, [name]: ''})

            if (name === 'yearTransport' ) {
                if (value > worldYear.getFullYear()) {
                    setTransportCarErrors({...transportCarErrors, [name]: greatValue})
                }
                if (value < 1890) {
                    setTransportCarErrors({...transportCarErrors, [name]: retroTransport})
                }
            }

            if (name === 'startPrice' && value > 1000000000000000000) {
                setTransportCarErrors({...transportCarErrors, [name]: greatValue})
            }
            if (name === 'motorSize' && value > 2147483647) {
                setTransportCarErrors({...transportCarErrors, [name]: greatValue})
            }
        }
        else if (transportCategory === 'bike'){
            setTransportMoto({
                ...transportMoto,
                [name]: value
            })

            value === ''
                ? setTransportMotoErrors({...transportMotoErrors, [name]: emptyFieldErrorMessage})
                :  e.target.value <= 0
                    ? setTransportMotoErrors({...transportMotoErrors, [name]: negativeErrorMessage})
                    : setTransportMotoErrors({...transportMotoErrors, [name]: ''})

            if (name === 'yearTransport' ) {
                if (value > worldYear.getFullYear()) {
                    setTransportMotoErrors({...transportMotoErrors, [name]: greatValue})
                }
                if (value < 1890) {
                    setTransportMotoErrors({...transportMotoErrors, [name]: retroTransport})
                }
            }

            if (name === 'startPrice' && value > 1000000000000000000) {
                setTransportMotoErrors({...transportMotoErrors, [name]: greatValue})
            }
            if (name === 'motorSize' && value > 2147483647) {
                setTransportMotoErrors({...transportMotoErrors, [name]: greatValue})
            }
        }
    }

    // изменение значения инпутов по дропдауну
    function handleChangeModelDrop({name, value}) {
        if (transportCategory === 'car'){
            setTransportCar({...transportCar, model: {value: value, name:name}})
            setTransportCarErrors({...transportCarErrors, 'model': ''})
        }
       else if (transportCategory === 'bike'){
            setTransportMoto({...transportMoto, model: {value: value, name:name}})
            setTransportMotoErrors({...transportMotoErrors, 'model': ''})
        }
    }
    function handleChangeBrandDrop({name, value}) {
        dispatch(getModel(value))

        setIsOptionBrandDown(false)
        
        if (transportCategory === 'car'){
            setTransportCar({...transportCar,model: {name:'', value: ''}, brand: {value: value, name:name}})
            setTransportCarErrors({...transportCarErrors, 'model': 'Не забудьте выбрать модель', 'brand': ''})
        }
        else if (transportCategory === 'bike'){
            setTransportMoto({...transportMoto, brand: {value: value, name:name}})
            setTransportMotoErrors({...transportMotoErrors, 'brand': ''})
        }
    }
  
    function handleChangeMotorDrop(name,value) {
        setIsOptionMotorDown(false)

        setTransportCar({...transportCar, 'motor': {name:name, value: value}})
        setTransportCarErrors({...transportCarErrors, 'motor': ''})
    }
    function handleChangeTransmissionDrop(name, value) {
        setIsOptionTransmissionDown(false)

        setTransportCar({...transportCar, 'transmission': {name:name, value: value}})
        setTransportCarErrors({...transportCarErrors, 'transmission': ''})
    }
    function handleChangeMotorSizeDrop(value) {
        setIsOptionMotorSizeDown(false)

        if (transportCategory === 'car') {
            setTransportCar({...transportCar, 'motorSize': value})
            setTransportCarErrors({...transportCarErrors, 'motorSize': ''})
        } else if (transportCategory === 'bike'){
            setTransportMoto({...transportMoto, 'motorSize': value})
            setTransportMotoErrors({...transportMotoErrors, 'motorSize': ''})
        }
    }

    // изменение и валидация специфического поля календаря
    useEffect(() => {
        if (transportCategory === 'car') {
            if (startDate === null) {
                setTransportCarErrors({...transportCarErrors, 'calendar': calendarErrorMessage})
                setIsValid(false)
            } else {
                setTransportCarErrors({...transportCarErrors, 'calendar': ''})
                setIsValid(true)
            }

        } else {
            if (startDate === null) {
                setTransportMotoErrors({...transportMotoErrors, 'calendar': calendarErrorMessage})
                setIsValid(false)
            } else {
                setTransportMotoErrors({...transportMotoErrors, 'calendar': ''})
                setIsValid(true)
            }
        }
    },[startDate])

///////////


    function getModal () {
        setOpenModal(!openModal)
    }

    function handleChangeCheckbox() {
        setIsChecked(!isChecked)
    }

    function handleSubmit(e) {
        e.preventDefault();
        if(transportCategory === 'car'){
           dispatch(updateCars(carItem.id, transportCar))
        }else{
            dispatch(updateBike(bikeItem.id, transportMoto))
        }
    }

    function updateEdit () {
        setEdit(!edit)
    }

    return (
        <div className="edit edit-transport">
            <div className={isOpened ? "popup popup_opened" : "popup" }>
                <div className="popup__container__transport-edit popup__container-edit">
                    <div className="popup__header">
                        <button className="popup__go-back-button"></button>
                        <h3 className="title popup__title">
                            {transportCategory === 'car' ?
                                <span className={"card-icon car"}></span> :
                                <span className={"card-icon bike"}></span>
                            }
                            Транспорт
                        </h3>
                        <button className="close-button" onClick={handleClose}></button>
                    </div>
                    { transportCategory === "car" ?
                        <div className="edit-chart">
                            <div className="edit-chart-box transport__edit__chart">
                                <p className="paragraph_l all-no-margin">{transportCar.endPrice}</p>
                                <p className="paragraph_m text-green all-no-margin">{`↑ ${transportCar.profit} (${transportCar.income}%)`}</p>
                            </div>
                        </div>
                        :
                        <div className="edit-chart">
                            <div className="edit-chart-box transport__edit__chart">
                                <p className="paragraph_l all-no-margin">{transportMoto.endPrice}</p>
                                <p className="paragraph_m text-green all-no-margin">{`↑ ${transportMoto.profit} (${transportMoto.income}%)`}</p>
                            </div>
                        </div>
                    }

                    <div className="edit-form">
                        <div className="edit-on" onClick={getModal}>
                            <div className={openModal === false ? "edit-on-button" : "edit-on-button display-block"} onClick={updateEdit}>
                                <p className="paragraph button-edit button-top">{edit === true ? "Отмена" : "Редактировать"}</p>
                                <p className="paragraph button-edit button-bottom">Удалить</p>
                            </div>
                        </div>
                        <details className="drop-edit" open>
                            {transportCategory === "car" ?
                            <summary className="paragraph drop-edit-text">{transportCar.transport}</summary>
                                :
                            <summary className="paragraph drop-edit-text">{transportMoto.transport}</summary>
                            }
                            {transportCategory === "car" ?

                            <form name="realty-residential" className={edit === true ? "form popup__form__transport__active-edit" :"form popup__form__transport__disabled-edit" } onSubmit={handleSubmit}>
                                <fieldset className="popup-fildset inputs-fildset">

                                    <div className="form-flex-wraper">
                                        <div className="form-flex-wraper__input-dropdown-box">
                                            <label className="active-form__label flex-label"> Марка
                                                <input type="text" name='brand' className={edit === false ? "form__input input-disabled" : "form__input bg-white-light"} placeholder="Киа" disabled={!edit} value={transportCar.brand.name} onClick={(e) => {dispatch(getBrand()); setIsOptionBrandDown(!isOptionBrandDown)}}></input>
                                                <span className = "form__item-error bg-gray-edit">{transportCarErrors.brand}</span>
                                            </label>

                                                <div className={!isOptionBrandDown ? 'dropdown': 'dropdown dropdown_open'} >
                                                    {brands.map((item) => {
                                                        return <div className="option" key={`brands_car_${item.id}`} onClick={(e) => {handleChangeBrandDrop({name: item.name, value: item.id})}}>{item.name}</div>
                                                    })}
                                                </div>
                                        </div>

                                        <div className='input-dropdown-box'>
                                            <label className="active-form__label flex-label"> Модель
                                                <input type="text" className={edit === false ? "form__input input-disabled" : "form__input bg-white-light"} placeholder="Сид" name='model' value={transportCar.model.name} disabled={!edit} onClick={(e) => {setIsOptionModelDown(!isOptionModelDown)}}></input>
                                                <span className = "form__item-error bg-gray-edit">{transportCarErrors.model}</span>
                                            </label>
                                            <div className={!isOptionModelDown ? 'dropdown': 'dropdown dropdown_open'} >
                                                {models.map((item) => {
                                                    return <div className="option" key={`models_car_${item.id}`} onClick={(e) => {handleChangeModelDrop({name: item.name, value: item.id})}}>{item.name}</div>
                                                })}
                                            </div>
                                        </div>
                                    </div>

                                    <label className="active-form__label"> Пробег
                                        <input type="number" className={edit === false ? "form__input input-disabled" : "form__input bg-white-light"} placeholder="150 000" name='mileage' value={transportCar.mileage} disabled={!edit} onChange={handleChangeInput}></input>
                                        <span className = "form__item-error bg-gray-edit">{transportCarErrors.mileage}</span>
                                    </label>
                                        <>
                                            <div className="form-flex-wraper">
                                                <div className='input-dropdown-box'>
                                                    <label className="active-form__label flex-label">Тип двигателя
                                                        <input name="motor" className={edit === false ? "form__input input-disabled" : "form__input bg-white-light"} placeholder="4Rmazg" disabled={!edit} value={transportCar.motor.name} onClick={(e) => {setIsOptionMotorDown(!isOptionMotorDown)}}></input>
                                                        <span className = "form__item-error bg-gray-edit">{transportCarErrors.motor}</span>
                                                    </label>
                                                    <div className={!isOptionMotorDown ? 'dropdown': 'dropdown dropdown_open'} >
                                                        {motorTypeOptions.map((item) => {
                                                            return <div className="option" key={item.name} onClick={(e) => {handleChangeMotorDrop(item.name,item.value)}}>{item.name}</div>
                                                        })}
                                                    </div>
                                                </div>

                                                <div className='input-dropdown-box'>
                                                    <label className="active-form__label flex-label"> Объем двигателя, л
                                                        <input type="number" className={edit === false ? "form__input input-disabled" : "form__input bg-white-light"} placeholder="150" name='motorSize' value={transportCar.motorSize} disabled={!edit}  onClick={(e) => {setIsOptionMotorSizeDown(!isOptionMotorSizeDown)}}></input>
                                                        <span className = "form__item-error bg-gray-edit">{transportCarErrors.motorSize}</span>
                                                    </label>
                                                    <div className={!isOptionMotorSizeDown ? 'dropdown': 'dropdown dropdown_open'} >
                                                        {motorSizeOptions.map((item) => {
                                                            return <div className="option" key={item} onClick={(e) => {handleChangeMotorSizeDrop(item)}}>{item}</div>
                                                        })}
                                                    </div>
                                                </div>
                                            </div>

                                            <div className="form-flex-wraper">
                                                <label className="active-form__label flex-label"> Год
                                                    <input type="number" name='yearTransport' className={edit === false ? "form__input input-disabled" : "form__input bg-white-light"} placeholder="2001" disabled={!edit} value={transportCar.yearTransport} onChange={handleChangeInput}></input>
                                                    <span className = "form__item-error bg-gray-edit">{transportCarErrors.yearTransport}</span>
                                                </label>

                                                <div className='input-dropdown-box'>
                                                    <label className="active-form__label flex-label"> Коробка передач
                                                        <input type="text" name='transmission' className={edit === false ? "form__input input-disabled" : "form__input bg-white-light"} placeholder="Автоматическая" disabled={!edit} value={transportCar.transmission.name} onClick={(e) => {setIsOptionTransmissionDown(!isOptionTransmissionDown)}}></input>
                                                        <span className = "form__item-error bg-gray-edit">{transportCarErrors.transmission}</span>
                                                    </label>
                                                    <div className={!isOptionTransmissionDown ? 'dropdown': 'dropdown dropdown_open'} >
                                                        {transmissionTypeOptions.map((item) => {
                                                            return <div className="option" key={item.name} onClick={(e) => {handleChangeTransmissionDrop(item.name,item.value)}}>{item.name}</div>
                                                        })}
                                                    </div>
                                                </div>
                                            </div>

                                            <label className="active-form__label date-label"> Дата покупки *

                                                <DatePicker
                                                    selected={startDate}
                                                    onChange={setStartDate}
                                                    name = 'startName'

                                                    locale='ru'
                                                    dateFormat={'dd.MM.yyyy'}
                                                    calendarStartDay={1}

                                                    showYearDropdown
                                                    showMonthDropdown
                                                    inputType='number'
                                                    disabled={!edit}
                                                />
                                            </label>
                                        </>

                                    <div className="form-flex-wraper">
                                        <label className="active-form__label flex-label"> Начальная цена
                                            <input type="number" className={edit === false ? "form__input input-disabled" : "form__input bg-white"} placeholder="10 000 000 ₽" name='startPrice' value={transportCar.startPrice} disabled></input>
                                        </label>


                                        <label className="active-form__label flex-label"> Текущая цена
                                            <input type="text" className={edit === false ? "form__input input-disabled" : "form__input bg-white"} placeholder="" name='endPrice' value={transportCar.endPrice + ' ' + '₽'} disabled></input>
                                        </label>
                                    </div>
                                    <div className="form-flex-wraper">
                                        <label className="active-form__label flex-label"> Доходность
                                            <input type="text" className={edit === false ? "form__input input-disabled" : "form__input bg-white"} placeholder='' name='income' value={'+' + ' ' + transportCar.income + '%'} disabled></input>
                                        </label>

                                        <label className="active-form__label flex-label"> Прибыль
                                            <input type="text" className={edit === false ? "form__input input-disabled" : "form__input bg-white"} placeholder="" name='floor' value={transportCar.profit + ' ' + '₽'} disabled></input>
                                        </label>
                                    </div>
                                </fieldset>
                                <div className="button__edit-box">
                                    <label className="checkbox center checkbox-edit" >
                                        <input type="checkbox" className="checkbox-input" checked={transportCar.isStatistic} onChange={handleChangeCheckbox}></input>
                                        <span className='checkbox-switch'></span>
                                        <span className='checkbox-text'>Отображать в общей статистике</span>
                                    </label>
                                    <input type='submit' className={edit === true ? "button orange-button button-no-margin button-custom" : "orange-button button-no-margin button-disabled"} onClick={updateEdit} value="Сохранить изменения" disabled={!isValid}></input>
                                </div>
                            </form>

                                : (transportCategory === "bike") ?

                            <form name="realty-residential" className={edit === true ? "form popup__form__transport__active-edit" :"form popup__form__transport__disabled-edit" } onSubmit={handleSubmit}>
                                <fieldset className="popup-fildset inputs-fildset">

                                    <div className="form-flex-wraper">
                                        <label className="active-form__label flex-label"> Марка
                                            <input type="text" name='brand' className={edit === false ? "form__input input-disabled" : "form__input bg-white-light"} placeholder="Киа" disabled={!edit} value={transportMoto.brand} onChange={(e) => {handleChangeInput(e)}}></input>
                                            <span className = "form__item-error bg-gray-edit">{transportMotoErrors.brand}</span>
                                        </label>

                                        <label className="active-form__label flex-label"> Модель
                                            <input type="text" className={edit === false ? "form__input input-disabled" : "form__input bg-white-light"} placeholder="Сид" name='model' value={transportMoto.model} disabled={!edit} onChange={(e) => {handleChangeInput(e)}}></input>
                                            <span className = "form__item-error bg-gray-edit">{transportMotoErrors.model}</span>
                                        </label>
                                    </div>

                                    <label className="active-form__label"> Пробег
                                        <input type="number" className={edit === false ? "form__input input-disabled" : "form__input bg-white-light"} placeholder="150 000" name='mileage' value={transportMoto.mileage} disabled={!edit} onChange={handleChangeInput}></input>
                                        <span className = "form__item-error bg-gray-edit">{transportMotoErrors.mileage}</span>
                                    </label>

                                    <div className="form-flex-wraper">

                                        <label className="active-form__label flex-label"> Объем двигателя, куб.см
                                            <input type="number" className={edit === false ? "form__input input-disabled" : "form__input bg-white-light"} placeholder="150" name='motorSize' value={transportMoto.motorSize} disabled={!edit} onChange={handleChangeInput}></input>
                                            <span className = "form__item-error bg-gray-edit">{transportMotoErrors.motorSize}</span>
                                        </label>

                                        <label className="active-form__label flex-label"> Год
                                            <input type="number" name='yearTransport' className={edit === false ? "form__input input-disabled" : "form__input bg-white-light"} placeholder="2000" disabled={!edit} value={transportMoto.yearTransport} onChange={handleChangeInput}></input>
                                            <span className = "form__item-error bg-gray-edit">{transportMotoErrors.yearTransport}</span>

                                        </label>
                                    </div>

                                    <label className="active-form__label date-label"> Дата покупки *

                                        <DatePicker
                                            selected={startDate}
                                            onChange={setStartDate}
                                            name = 'startName'

                                            locale='ru'
                                            dateFormat={'dd.MM.yyyy'}
                                            calendarStartDay={1}

                                            showYearDropdown
                                            showMonthDropdown
                                            inputType='number'
                                            disabled={!edit}
                                        />
                                    </label>

                                    <div className="form-flex-wraper">
                                        <label className="active-form__label flex-label"> Начальная цена
                                            <input type="number" className={edit === false ? "form__input input-disabled" : "form__input bg-white"} placeholder="10 000 000 ₽" name='startPrice' value={transportMoto.startPrice} disabled></input>
                                            <span className = "form__item-error bg-gray-edit">{transportMotoErrors.startPrice}</span>
                                        </label>


                                        <label className="active-form__label flex-label"> Текущая цена
                                            <input type="text" className={edit === false ? "form__input input-disabled" : "form__input bg-white"} placeholder="" name='endPrice' value={transportMoto.endPrice + ' ' + '₽'} disabled></input>
                                        </label>
                                    </div>
                                    <div className="form-flex-wraper">
                                        <label className="active-form__label flex-label"> Доходность
                                            <input type="text" className={edit === false ? "form__input input-disabled" : "form__input bg-white"} placeholder='' name='income' value={'+' + ' ' + transportMoto.income + '%'} disabled></input>
                                        </label>

                                        <label className="active-form__label flex-label"> Прибыль
                                            <input type="text" className={edit === false ? "form__input input-disabled" : "form__input bg-white"} placeholder="" name='floor' value={transportMoto.profit + ' ' + '₽'} disabled></input>
                                        </label>
                                    </div>
                                </fieldset>
                                <div className="button__edit-box">
                                    <label className="checkbox center checkbox-edit" >
                                        <input type="checkbox" className="checkbox-input" checked={transportMoto.isStatistic} onChange={handleChangeCheckbox}></input>
                                        <span className='checkbox-switch'></span>
                                        <span className='checkbox-text'>Отображать в общей статистике</span>
                                    </label>
                                    <input type='submit' className={edit === true ? "button orange-button button-no-margin button-custom" : "orange-button button-no-margin button-disabled"} onClick={updateEdit} value="Сохранить изменения" disabled={!isValid}></input>
                                </div>
                            </form>
                                    : ''
                            }
                        </details>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default TransportEdit